<template>
    <div class="text-center">
        <v-dialog v-model="$store.state.newMessageDialog" max-width="600">
            <template v-slot:activator="{ props: activatorProps }">
                <v-btn append-icon="mdi-chat" color="green" variant="flat" class="text-none px-2"
                    @click="$store.state.newMessageDialog = !$store.state.newMessageDialog">New</v-btn>
            </template>
            <v-card flat class="pa-5" rounded="xl">
                <!-- Back button -->

                <v-card-title>
                    <v-btn class="ms-n6" icon @click="$store.state.newMessageDialog = false" variant="text">
                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                    Search for contact
                </v-card-title>
                <v-text-field v-model="name" label="Search" hide-details required class="my-2" variant="outlined"
                    density="compact" prepend-inner-icon="mdi-magnify">
                </v-text-field>
                <v-chip-group v-model="selectedTag" @click="handleChipChange" selected-class="text-primary" mandatory>
                    <v-chip v-for="tag in tags" :key="tag" :value="tag" outlined>{{ tag }}</v-chip>
                </v-chip-group>
                <!-- <v-row align="center">
                    <v-col>
                        <p>Recent</p>
                        <v-infinite-scroll class="custom-scrollbar hover-scroll-container" direction="horizontal"
                            @load="load">
                            <template v-for="(contact, index) in horizontalContacts" :key="contact.id">
                                <div class="text-center pa-2">
                                    <v-avatar size="40" class="avatar-circle">
                                        <v-img :src="contact.image" />
                                    </v-avatar>
                                    <v-card-title><span style="font-size:16px">{{ contact.name }}</span></v-card-title>
                                </div>
                            </template>
                        </v-infinite-scroll>
                    </v-col>
                </v-row> -->
                <v-row>
                    <v-col>
                        <p>Contacts</p>
                        <v-list lines="two">
                            <template v-for="(item, index) in filteredcontacts" :key="item.value">
                                <v-list-item @click="selectContact(item)">
                                    <v-list-item-avatar>
                                        <v-avatar>
                                            <v-img :src="item.image"></v-img>
                                        </v-avatar>
                                        {{ item.name }}
                                        <v-chip :color="item.chip_color" label size="x-small">
                                            {{ item.chip }}
                                        </v-chip>
                                    </v-list-item-avatar>
                                    <v-list-item-content>
                                        <v-list-item-subtitle>
                                            <div v-html="item.subtitle"></div>
                                        </v-list-item-subtitle>
                                    </v-list-item-content>

                                </v-list-item>
                                <v-divider v-if="index < filteredcontacts.length - 1" inset></v-divider>
                            </template>
                        </v-list>
                    </v-col>
                </v-row>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import { useAxios } from '@/composables/useAxios';
export default {
    emits: ['sendContact'],
    data: () => ({
        name: "",
        selectedTag: 'Center', // Default selected tag
        horizontalContacts: [
            { id: 1, image: "https://cdn.vuetifyjs.com/images/profiles/marcus.jpg", name: "Marcus" },
            { id: 2, image: "https://insertface.com/fb/910/business-formal-attire-woman-910174-d4g5p-fb.jpg", name: "Jessica" },
            { id: 3, image: "https://img.freepik.com/free-photo/portrait-expressive-young-man-wearing-formal-suit_273609-6942.jpg", name: "John" },
            { id: 4, image: "https://www.shutterstock.com/image-photo/young-woman-formal-clothes-on-260nw-1081627103.jpg", name: "Anna" },
            { id: 5, image: "https://img.freepik.com/free-photo/beautiful-young-man-student-businessman-jacket-holds-his-arms-crossed-isolated-light-grey-wall_231208-6135.jpg", name: "Michael" }
        ],
        tags: [
            'All',
            'Center',
            'Class Room',
            'Teacher',
            'Support',
        ],
        contacts: [],
    }),
    computed: {
        filteredcontacts() {
            return this.contacts.filter(item =>
                (this.selectedTag === 'All' || item.chip === this.selectedTag) && // Filter by chip if selectedTag is not 'All'
                (this.name.trim() === "" || item.name.toLowerCase().includes(this.name.toLowerCase())) // Filter by text if not empty
            );
        }
    },
    async created() {
        await this.getCenters();
        await this.getClassRooms();
        await this.getTeachers();

    },
    methods: {
        ...useAxios(),
        async getCenters() {
            const businessUnits = await this.get('business-units')
            businessUnits.forEach(unit => {
                this.contacts.push(
                    {
                        image: unit.image,
                        name: unit.name,
                        type:'businessunit',
                        subtitle: '<span class="ms-5 mt-2">&nbsp&nbsp&nbsp&nbsp Address:&nbsp' + unit.address + '</span>',
                        chip: "Center",
                        chip_color: "orange-darken-5",
                        value: unit.id,
                        id: unit.id,
                        terminalType: 1,
                    }
                )
            });
            return businessUnits;
        },
        async getClassRooms() {
            const classRooms = await this.get('parent-classrooms')
            classRooms.forEach(classroom => {
                this.contacts.push(
                    {
                        image: 'https://static.vecteezy.com/system/resources/previews/017/395/362/non_2x/google-classroom-icon-free-png.png',
                        name: classroom.name,
                        type:'classroom',
                        subtitle: '<span class="ms-5 mt-2">&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp' + classroom.business_unit_name + '</span>',
                        chip: "Class Room",
                        chip_color: "orange-darken-1",
                        value: classroom.id,
                        id: classroom.id,
                        terminalType: 2,
                    }
                )
            });
            return classRooms;
        },
        async getTeachers() {
            const teachers = await this.get('parent-teachers')
            teachers.forEach(teacher => {
                this.contacts.push(
                    {
                        image: teacher.profile.image,
                        name: teacher.name,
                        type:'teacher',
                        subtitle: '<span class="ms-5 mt-2">&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp' + teacher.business_unit_name + '</span>',
                        chip: "Teacher",
                        chip_color: "indigo-darken-4",
                        value: teacher.id,
                        id: teacher.id,
                        terminalType: 4,
                    }
                )
            });
            return teachers;
        },
        selectContact(item) {
            this.$emit('sendConversation', item);
            this.$store.state.newMessageDialog = false;
        },
        handleChipChange() {
            // Clear search text when chip changes
            this.name = "";
        },
        load({ done }) {
            setTimeout(() => {
                // Example: Appending 5 more contacts
                for (let i = 6; i <= 10; i++) {
                    this.horizontalContacts.push({
                        id: i,
                        image: `https://example.com/image_${i}.jpg`, // Replace with your image URLs
                        name: `Contact ${i}`
                    });
                }
            }, 1000); // Simulating a 1-second delay before loading more contacts
        },
    }
};
</script>

<style scoped>
/* Add circular border around avatar images */
.avatar-circle {
    border: 2px solid #fff;
    /* White border */
    border-radius: 50%;
    /* Make it circular */
}

/* Custom scrollbar styles */
.custom-scrollbar {
    overflow-x: auto;
    white-space: nowrap;
}

.custom-scrollbar::-webkit-scrollbar {
    height: 8px;
    /* Horizontal scrollbar height */
}

.custom-scrollbar::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.5);
    /* Darker color for scrollbar thumb */
    border-radius: 4px;
    /* Rounded corners */
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0, 0, 0, 0.7);
    /* Darker color on hover */
}

.custom-scrollbar::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.1);
    /* Lighter color for scrollbar track */
}
</style>
