<template>
  <div class="pa-5 mt-3">
    <v-breadcrumbs :items="breads">
      <template v-slot:prepend>
        <v-icon size="small" icon="$vuetify" class="text-primary"></v-icon>
      </template>
    </v-breadcrumbs>
    <v-row justify="center" align="center">
      <TransitionGroup name="fade" class="container">
        <v-col
          align="center"
          v-for="child in $store.state.children.children"
          :key="child.id"
          cols="12"
          sm="6"
          md="4"
          lg="3"
          xl="2"
        >
          <v-card
            rounded="xl"
            width="300"
            class="pa-5"
            link
            hover
            @click="
              this.$router.push({ name: 'child', params: { id: child.id } })
            "
          >
            <v-card-title align="center">
              <v-avatar
                class="elevation-10"
                :image="child.image ? child.image : 'https://cdn-icons-png.flaticon.com/512/7084/7084446.png'"
                size="100"
              ></v-avatar>
              <p class="text-blue-darken-4 text-h5 mt-3">{{ child.first_name }}</p>
              <p class="text-green text-h7 mt-1">Age {{ child.age }}</p>
            </v-card-title>
            <v-card-text align="center" class="mt-2">
  
              <v-divider class="my-3"></v-divider>
              <v-btn class="text-none text-primary" variant="plain"
                >View
                <template v-slot:prepend>
                  <v-icon class="text-primary">mdi-eye</v-icon>
                </template>
              </v-btn>
              <v-btn class="text-none text-primary" variant="plain" @click.stop="editChild(child)"
                >Edit
                <template v-slot:prepend>
                  <v-icon class="text-success">mdi-pen</v-icon>
                </template>
              </v-btn>
              <v-btn
                class="text-none text-red"
                variant="plain"
                @click.stop="deleteChild(child.id)"
                >Delete
                <template v-slot:prepend>
                  <v-icon class="text-red">mdi-delete</v-icon>
                </template>
              </v-btn>
            </v-card-text>
          </v-card>
        </v-col>

        <add-child-dialog  @fetchNewChild="fetchNewChild"/>
      </TransitionGroup>
    </v-row>
    <v-row>
      <dynamic-form
      v-if="editingChild"
      :model-url="'children'"
      :instance-id="editingChild.id"
      :foreignKeys="dynamicFormForeignKeys"
      :visible="showDialog"
      @updateVisible= "showDialog = $event"
      @saved="handleSave"
      ></dynamic-form>
    </v-row>
  </div>
</template>
<script>
import AddChildDialog from "../components/children/AddChildDialog.vue";
import { useAxios } from '@/composables/useAxios';
import DynamicForm from "@/components/global/DynamicForm.vue";

export default {
  components: { AddChildDialog, DynamicForm },
  async created() {
    await this.fetchChildren();
},
  methods: {
    ...useAxios(),
    editChild(child)
    {
      this.editingChild = child;
      this.showDialog = true
    },
    handleSave(visible) {
      this.showDialog = visible;
      this.fetchChildren();
      this.editingChild = null;
    },
    async fetchChildren() {
      this.$store.state.children.children = await this.get('children');
    },
    fetchNewChild(child) {
      this.$store.state.children.push(child);
    },
    async deleteChild(id) {
        await this.del('children/' + id).then(() => {
          this.$store.state.children.children = this.$store.state.children.children.filter((child) => child.id != id);
        }).catch((error) => {
          
        });
    },
  },
  
  data() {
    return {
      showDialog: false,
      editingChild: null,
      dynamicFormForeignKeys:[{ field: 'gender', api: 'genders', displayField: 'name' }],
      loading:true,
      children: [],
      breads: [
        {
          title: "Dashboard",
          disabled: false,
          href: "/",
        },
        {
          title: "Children",
          disabled: true,
          href: "/children",
        },
      ],
    };
  },
};
</script>

<style scoped lang="scss">
$avatar-elevation: 10;

.profile-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 400px;
  width: 100%;
  border-radius: 25px;
  padding: 30px;
  border: 1px solid #ffffff40;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4);
}
.image {
  position: relative;
  height: 100px;
  width: 100px;
}
.image .profile-pic {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4);
}
.data {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px;
}
.data h2 {
  font-size: 33px;
  font-weight: 600;
}
span {
  font-size: 18px;
}
.row {
  display: flex;
  align-items: center;
  margin-top: 30px;
}
.row .info {
  text-align: center;
  padding: 0 20px;
}
.buttons {
  display: flex;
  align-items: center;
  margin-top: 30px;
}
.buttons .btn {
  color: #fff;
  text-decoration: none;
  margin: 0 20px;
  padding: 8px 25px;
  border-radius: 25px;
  font-size: 18px;
  white-space: nowrap;
  background: linear-gradient(to left, #33ccff 0%, #ff99cc 100%);
}
.buttons .btn:hover {
  box-shadow: inset 0 5px 20px rgba(0, 0, 0, 0.4);
}
</style>
