// src/plugins/push_notifications.js
import { messaging, getToken, onMessage } from '../firebase';

// Function to request notification permission
function requestNotificationPermission() {
  return Notification.requestPermission().then(permission => {
    if (permission !== 'granted') {
      throw new Error('Notification permission not granted');
    }
    return permission;
  });
}

// Function to register the service worker
function registerServiceWorker() {
  return navigator.serviceWorker.register('/firebase-messaging-sw.js')
    .then((registration) => {
      console.log('Service Worker registered with scope:', registration.scope);
      return registration;
    })
    .catch((error) => {
      console.error('Service Worker registration failed:', error);
      throw error;
    });
}

// Function to get FCM token
function retrieveFCMToken(registration) {
  const vapidKey = 'BPgJ2gG-7oTsHZ95CV7X0tst5BZQ8Nohtcxe5reUbuwAWX8SI_rI2tduxIMbKOxWLITi8KSbY5V_MbSQVC5CLz4';
  return getToken(messaging, { vapidKey, serviceWorkerRegistration: registration })
    .then((currentToken) => {
      if (!currentToken) {
        throw new Error('No registration token available. Request permission to generate one.');
      }
      console.log('FCM Token:', currentToken);
      // Send the token to your server
      return currentToken;
    })
    .catch((err) => {
      console.log('An error occurred while retrieving token.', err);
      throw err;
    });
}

// Function to handle foreground messages
function handleForegroundMessages() {
  onMessage(messaging, (payload) => {
    console.log('Message received.', payload);
    const notificationTitle = payload.notification.title;
    const notificationOptions = {
      body: payload.notification.body,
      icon: '/firebase-logo.png'
    };
    new Notification(notificationTitle, notificationOptions);
  });
}

// Initialize Firebase Cloud Messaging
function initializeFCM() {
  if (Notification.permission === 'default') {
    requestNotificationPermission()
      .then(() => registerServiceWorker())
      .then(registration => retrieveFCMToken(registration))
      .then(() => handleForegroundMessages())
      .catch(error => console.error(error.message));
  } else if (Notification.permission === 'granted') {
    registerServiceWorker()
      .then(registration => retrieveFCMToken(registration))
      .then(() => handleForegroundMessages())
      .catch(error => console.error(error.message));
  } else {
    console.log('Notification permission denied.');
  }
}

// Export the initialize function
export { initializeFCM };
