<template>
  <v-card class="mx-auto">
    <v-row style="height: 70px" align="center">
      <v-spacer></v-spacer>
      <NotesDialog />
    </v-row>
    <TransitionGroup name="fade" class="container">
      <v-list :items="items" lines="three" item-props>
        <template v-slot:subtitle="{ item }">
          <div>
            <span class="text-primary">{{ item.date }}</span> &mdash;
            {{ item.subtitle }}
          </div>
        </template>
        <template v-slot:append="{ item }">
          <v-btn
            color="green"
            icon="mdi-pen"
            variant="text"
            @click="editRecord(item)"
          ></v-btn>
          <v-btn
            color="red"
            icon="mdi-delete"
            variant="text"
            @click="deleteRecord(item)"
          ></v-btn>
        </template>
      </v-list>
    </TransitionGroup>
  </v-card>
</template>

<script>
import NotesDialog from "./NotesDialog.vue";
export default {
  components: {
    NotesDialog,
  },
  methods: {
    deleteRecord(item) {
      this.items = this.items.filter((record) => record.value !== item.value);
    },
    editRecord(item) {
      this.$store.commit("editRecord", item);
    },
  },

  data() {
    return {
      items: [
        { type: "subheader", title: "Child Notes" },
        {
          prependIcon: "mdi-pen",
          title: "Morning Activities",
          value: 0,
          subtitle:
            "Engaged children in storytelling and creative drawing activities.",
          date: "1/3/2024",
        },
        { type: "divider", inset: true },
        {
          prependIcon: "mdi-pen",
          title: "Outdoor Playtime",
          value: 1,
          subtitle:
            "Supervised outdoor play session. Encouraged teamwork and physical exercise.",
          date: "5/10/2023",
        },
        { type: "divider", inset: true },
        {
          prependIcon: "mdi-pen",
          title: "Arts and Crafts",
          value: 2,
          subtitle:
            "Facilitated an arts and crafts session. Children created colorful artworks using various materials.",
          date: "12/7/2022",
        },
        { type: "divider", inset: true },
        {
          prependIcon: "mdi-pen",
          title: "Snack Time",
          value: 3,
          subtitle:
            "Provided nutritious snacks and encouraged healthy eating habits.",
          date: "8/22/2021",
        },
        { type: "divider", inset: true },
        {
          prependIcon: "mdi-pen",
          title: "Nap Time",
          value: 4,
          subtitle:
            "Ensured a peaceful nap time environment. Read stories to help children relax before nap.",
          date: "3/15/2020",
        },
      ],
    };
  },
};
</script>
