<template>
  <v-container fluid>
    <v-row align="start">
      <v-col class="v-col-md-4 v-col-xl-3 v-col-12">
        <v-card rounded="xl" class="pa-5 profile-card" elevation="0">
          <v-card-title align="center">
            <v-avatar
              class="elevation-10"
              :image="child.image ? child.image : 'https://cdn-icons-png.flaticon.com/512/7084/7084446.png'"
              size="100"
            ></v-avatar>
            <p class="text-blue-darken-4 text-h5 mt-3">
              {{ child.first_name }}
            </p>
            <p class="text-green text-h7 mt-1">Age {{ child.age }}</p>
            <p class="text-green text-h7 mt-1" v-if="child.classroom_name">{{ child.classroom_name }}</p>
          </v-card-title>
        
        </v-card>
      </v-col>
      <v-col class="v-col-md-8 v-col-xl-9 v-col-12">
        <!-- <v-card rounded="xl" class="pa-3 profile-card" elevation="0">
          <v-card-title>About the child</v-card-title>
          <v-divider class="my-3"></v-divider>
          <p class="pa-2">
            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Illum iste
            laboriosam delectus molestiae, officiis et quae non, rerum quia,
            esse voluptates earum. Magni eum voluptatum, ipsam quod minus porro
            amet?
          </p>
        </v-card> -->
        <v-card rounded="xl" class="pa-3 profile-card" elevation="0">
          <v-card-title>Personal Details</v-card-title>
          <v-divider class="my-3"></v-divider>
          <v-row class="px-5">
            <v-col>
              <div class="info">
                <p class="text-lightText mb-1 info">Full Name</p>
                <p class="font-weight-medium info">
                  {{ child.first_name }}
                </p>
              </div>
            </v-col>
            <v-col>
              <div class="info">
                <p class="text-lightText mb-1 info">Father Name</p>
                <p class="font-weight-medium info">{{ child.last_name }}</p>
              </div>
            </v-col>
            <v-divider></v-divider>
          </v-row>
          <v-row class="px-5">
            <v-col>
              <div class="info">
                <p class="text-lightText mb-1 info">Gender</p>
                <p class="font-weight-medium info">
                  {{ child.gender_name }}
                </p>
              </div>
            </v-col>
            <v-col>
              <div class="info">
                <p class="text-lightText mb-1 info">Age</p>
                <p class="font-weight-medium info">
                  {{ child.age }}
                </p>
              </div>
            </v-col>
            <v-divider></v-divider>
          </v-row>
        </v-card>
      </v-col>
    </v-row>

    <v-row>
      <!-- <v-col class="v-col-xl-4 v-col-12">
          
      </v-col> -->

      <v-col class="v-col-12">
          <v-card rounded="xl" class="pa-1 profile-card text-center" elevation="0" >
              <p class="pa-5">Child Activity Timeline</p>
              <v-select
              class="pa-5"
              v-model="selectedFilterOptions"
              :items="filterOptions"
              label="Filter"
              chips
              multiple
            ></v-select>

              <v-timeline dense clipped align="start" side="end" >
                  <v-timeline-item size="small" v-for="timeline in filteredTimelines" class="w-100" @click="timelineAction(timeline)">
                    <template #icon>
                      <v-avatar :image="timeline.icon" size="30"></v-avatar>
                    </template>
                    <v-card color="blue-grey-lighten-5 w-100" link hover >
                      <!-- Container with a class for dynamic width -->
                      <v-container
                        class="timeline-container pt-0 pb-0"
                        fluid
                      >
                      <v-row class="d-flex justify-space-between align-center no-gutters" style="width: 100%;">
                          <!-- Timeline Text -->
                          <v-col cols="12" md="7" :class="'timeline-text px-2 ' + ($vuetify.display.smAndDown ? 'pb-0' : '' )" style="flex-grow: 1; min-width: 0;">
                            <v-card-text class="py-1 text-nowrap text-start" v-html="timeline.text"></v-card-text>
                          </v-col>
                          <!-- Timeline Time -->
                          <v-col cols="8" md="3" :align="$vuetify.display.smAndDown ? 'start' : 'end'" :class="'px-2 ' + ($vuetify.display.smAndDown ? 'py-0' : 'mt-2')" style="white-space: nowrap;">
                            <v-card-text class="pt-1 text-teal-darken-2">{{ timeline.time }}</v-card-text>
                          </v-col>
                          <v-col>
                            <v-icon color="blue" v-if="timeline.action == 'Teaching Activity' ">mdi-eye</v-icon>
                          </v-col>
                
                          <!-- View Button -->
                          <!-- <v-col cols="2" md="1" align="end"  :class="'px-2 ' + ($vuetify.display.smAndDown ? 'py-0' : '') ">
                            <v-btn variant="text" :class="$vuetify.display.smAndDown ? 'mt-n4' : '' ">
                              <v-icon color="blue">mdi-eye</v-icon>
                            </v-btn>
                          </v-col> -->
                        </v-row>
                      </v-container>
                    </v-card>
                  </v-timeline-item>
                </v-timeline>
          </v-card>
      </v-col>
  </v-row>

  <!-- Image Dialog -->
  <v-dialog v-model="teachingActivityAttachmentsDialog" max-width="800px" scrollable>
                  <v-carousel
                    v-model="carouselIndex"
                    :items="previewImages"
                    show-arrows
                    height="800"
                    hide-delimiters
                  >
                  <template v-slot:prev="{ props }">
                    <v-btn color="info" icon class="custom-arrow" @click="carouselIndex = (carouselIndex - 1 + previewImages.length) % previewImages.length">
                      <v-icon>mdi-chevron-left</v-icon>
                    </v-btn>
                  </template>
                  <template v-slot:next="{ props }">
                    <v-btn color="info" icon class="custom-arrow" @click="carouselIndex = (carouselIndex - 1 + previewImages.length) % previewImages.length">
                      <v-icon>mdi-chevron-right</v-icon>
                    </v-btn>
                  </template>
                    <v-carousel-item
                      v-for="attachment in previewImages"
                      :key="attachment.id"
                    >
                      <v-img
                        :src="attachment.file"
                        alt="Image"
                        class="mx-auto"
                        contain
                      ></v-img>
                      <v-card-title class="text-center text-white bg-info mt-3" v-if="attachment.description">{{attachment.description}}</v-card-title>
                    </v-carousel-item>
                  </v-carousel>
                    <!-- Thumbnail Navigation -->
                    <v-row class="thumbnail-row" justify="center" align="center">
                      <v-btn
                        v-for="(attachment, index) in previewImages"
                        :key="attachment.id"
                        :class="{'thumbnail-active': index === carouselIndex}"
                        @click="carouselIndex = index"
                        variant="text"
                        class="thumbnail-btn"
                      >
                        <v-img
                          :src="attachment.file"
                          class="thumbnail-img"
                          contain
                          height="60px"
                          width="60px"
                        ></v-img>
                      </v-btn>
                    </v-row>
      </v-dialog>
  </v-container>
</template>

<script>
import { useAxios } from '@/composables/useAxios';

export default {
  watch: {},
  data() {
    return {
      child: null,
      timelines: null,
      filterOptions: ['Food', 'Diaper', 'Potty Training', 'Mood', 'Activity Part', 'Teaching Activity', 'Supplies', 'Health', 'Nap'],
      selectedFilterOptions: ['Food', 'Diaper', 'Potty Training', 'Mood', 'Activity Part', 'Teaching Activity', 'Supplies', 'Health', 'Nap', 'Attendance'],
      carouselIndex: 0,
      previewImages: [],
      teachingActivityAttachmentsDialog: false,
    };
  },
  async created() {
    
    this.child = this.$store.state.children.children.filter(
      (child) => child.id == this.$route.params.id
    )[0];

    const activity = this.$route.query.activity;
    if (activity) {
      // Set selectedFilterOptions to the value of the activity query parameter
      this.selectedFilterOptions = [activity];
    }
    
    this.getTimelines();
    
  },
  methods:
  {
    ...useAxios(),
    async getTimelines()
    {
      this.timelines = await this.get('child-data/' + this.child.id);
    },
    timelineAction(timeline) {
    if (timeline.action == 'Teaching Activity') {
      console.log("Attachments:", timeline.attachments); // Debugging line
      this.previewImages = timeline.attachments;
      this.teachingActivityAttachmentsDialog = true;
    }
  }
  },
  computed:
  {
    filteredTimelines()
    {
      if (!Array.isArray(this.timelines)) {
        return []; // Return an empty array if timelines is not an array
      }
  
    return this.timelines.filter(timeline => {
      return this.selectedFilterOptions.includes(timeline.action);
    });

    }
  }
};
</script>

<style scoped>
.profile-card {
  border: 1px solid rgb(218, 226, 226) !important;
  min-height: 270px;
}
.info {
  font-size: 1em;
}


</style>

<style>
.v-timeline .v-timeline-divider__inner-dot {
    background: transparent !important;
}
.v-timeline
{
    justify-content: start !important;
}
.mobile-class {
  width: 100%;
}
.v-timeline-item__body
{
  width: 100% !important;
}
</style>