import introJs from 'intro.js';

const state = {
  steps: [
    {
      element: '#home',
      intro: ' Welcome to the Daycare Center! Explore our services and facilities. ',
    },
    {
      element: '#centers',
      intro: ' Discover a variety of daycare centers. Find the perfect match for your child! ',
    },
    {
      element: '#center-name',
      intro: ' View detailed center information, including a descriptive name. ',
    },
    {
      element: '#center-address',
      intro: ' View detailed center address',
    },
    {
      element: '#center-age-range',
      intro: ' Find centers suitable for your child\'s age range. ',
    },
    {
      element: '#center-search',
      intro: ' Use our powerful search feature to quickly find the centers you need. ',
    },
    {
      element: '#center-filter',
      intro: ' Refine your search with advanced filtering options. Tailor your daycare experience. ',
    },
    {
      element: '#center-card',
      intro: ' Explore detailed information about a specific daycare center. Click to view!',
    },
    {
      element: '#card-children',
      intro: 'You can view your children and add them from here',
    },
    {
      element: '#a',
      intro: 'Have a nice day ',
    },
    // Add more steps as needed
  ],
  isTourActive: false,
};

const mutations = {
  START_TOUR(state) {
    state.isTourActive = true;
  },
  END_TOUR(state) {
    state.isTourActive = false;
  },
};

const actions = {
  startTour({ commit, state }) {
    commit('START_TOUR');
      introJs().setOptions({ steps: state.steps }).start();
  },
  refreshIntro() {
    introJs().refresh();
  },
  endTour({ commit }) {
    commit('END_TOUR');
  },
};

const getters = {
  tourSteps: (state) => state.steps,
  isTourActive: (state) => state.isTourActive,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
