<template>
    <v-dialog v-model="$store.state.mapDialog" >
        <v-card rounded="xl" elevation="10" class="pa-3 h-100">
            <v-card-title>
                <v-row>
                    <v-col>Nearest Centers</v-col>
                    <v-col align="end"><v-btn icon="mdi-close" variant="text"
                            @click="$store.state.mapDialog = false"></v-btn></v-col>
                </v-row>
            </v-card-title>

            <v-card-text>
                <v-row>
                    <v-col>
                        <div>
                            <div ref="map" class="map rounded-xl"></div>
                        </div>

                    </v-col>
                </v-row>
                <v-row>
                    <v-col><b class="text-subtitle-1">Nearest Centers List</b></v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-virtual-scroll :items="sortedCenters" height="300" compact>
                            <template v-slot:default="{ index, item }">
                                <v-list lines="one" compact>
                                    <v-list-item :key="item.name" :value="item.name" :prepend-avatar="item.image"
                                        @click="viewCenter(item)">
                                        <template v-slot:title>
                                            <span v-if="index === 0">{{ item.name }} <v-chip color="primary" label
                                                    size="small">Nearest</v-chip></span>
                                            <span v-else>{{ item.name }}</span>
                                        </template>
                                        <template v-slot:subtitle v-if="item.distance">
                                            <v-row align="center" >
                                                <v-col> <span class="text-subtitle-1" dir="rtl">{{ item.address }} - {{ item.distance.toFixed(2) }} KM</span> </v-col>
                                                <v-col align="end">
                                                    <v-btn color="primary" class="text-none me-2" icon="mdi-car" size="x-small" @click.stop="viewDirection(item, 'driving')" />
                                                    <v-btn color="primary" class="text-none" icon="mdi-walk" size="x-small" @click.stop="viewDirection(item, 'walking')" />
                                                </v-col>
                                            </v-row>

                                        </template>
                                    </v-list-item>
                                </v-list>
                            </template>
                        </v-virtual-scroll>
                    </v-col>
                </v-row>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
import { mapState } from 'vuex'; // Import mapState from Vuex

export default {
    props: ['centers'],
    name: 'Map',
    data() {
        return {
            map: null,
            markers: [],
            geocoder: null,
        };
    },
    mounted() {
        this.loadMap();
        this.geocodeAllCenters();
    },
    computed: {
        ...mapState(['userLocation']), // Map userLocation from Vuex state
        sortedCenters() {
            // Ensure centers have lat and lng properties before sorting
            const centersWithLocation = this.centers.filter(center => center.lat && center.lng);
            const centersWithoutLocation = this.centers.filter(center => !center.lat || !center.lng);

            // Sort centers with known location based on proximity to userLocation
            centersWithLocation.sort((a, b) => {
                const distA = this.calculateDistance(a);
                const distB = this.calculateDistance(b);
                return distA - distB;
            });

            // Combine sorted centers with those without location data
            return [...centersWithLocation, ...centersWithoutLocation];
        },
        nearestCenter() {
            if (this.sortedCenters.length > 0) {
                return this.sortedCenters[0]; // Assuming sortedCenters is sorted by proximity
            }
            return null;
        },
    },
    methods: {
        viewCenter(center) {
            this.$emit('viewCenter', center);
        },
        viewDirection(center, mode) {
            const travelMode = mode === 'driving' ? 'driving' : 'walking';
            const url = `https://www.google.com/maps/dir/?api=1&origin=${this.userLocation.lat},${this.userLocation.lng}&destination=${center.lat},${center.lng}&travelmode=${travelMode}`;
            window.open(url, '_blank');
        },
        loadMap() {
            this.map = new google.maps.Map(this.$refs.map, {
                center: { lat: -34.397, lng: 150.644 },
                zoom: 8,
            });
            this.geocoder = new google.maps.Geocoder();
            // set my location
            //getting my latlng
            this.geocoder.geocode({ address: this.$store.state.userInfo.profile.address }, (results, status) => {
                    if (status === 'OK') {
                        if (results[0]) {
                            const location = results[0].geometry.location;
                            this.$store.state.userLocation.lat = location.lat(); // Update center object with latitude
                            this.$store.state.userLocation.lng = location.lng(); // Update center object with longitude

                            const marker = new google.maps.Marker({
                            position: { lat: this.userLocation.lat, lng: this.userLocation.lng },
                            map: this.map,
                            icon: {
                                url: 'images/blue-dot.png',
                                scaledSize: new google.maps.Size(50, 50),
                            },
                            title: this.$store.state.userInfo.profile.address,
                        });
                        } else {
                            console.log('No geocode found for address: ' + this.$store.state.userInfo.profile.address);
                        }
                    } else {
                        console.log('Geocode was not successful for the following reason: ' + status);
                    }
            });
            

        },
        geocodeAllCenters() {
            this.clearMarkers();
            const bounds = new google.maps.LatLngBounds(); // Initialize bounds object

            this.centers.forEach(center => {
                this.geocoder.geocode({ address: center.address }, (results, status) => {
                    if (status === 'OK') {
                        if (results[0]) {
                            const location = results[0].geometry.location;
                            center.lat = location.lat(); // Update center object with latitude
                            center.lng = location.lng(); // Update center object with longitude
                            center.distance = this.calculateDistance(center);
                            this.createMarker(location, center); // Pass updated center object to createMarker
                            bounds.extend(location); // Extend bounds with each marker's position
                        } else {
                            console.log('No results found for center: ' + center.name);
                        }
                    } else {
                        console.log('Geocode was not successful for the following reason: ' + status);
                    }
                });
            });

            // Fit map to bounds after all markers are added
            setTimeout(() => {
                this.map.fitBounds(bounds); // Automatically adjusts zoom to fit all markers

                setTimeout(() => {
                    const nearestCenter = this.nearestCenter;
                    if (nearestCenter) {
                        const nearestMarkerIndex = this.markers.findIndex(marker => marker.getTitle() === nearestCenter.name);
                        if (nearestMarkerIndex !== -1) {
                            this.markers[nearestMarkerIndex].setIcon({
                                url: 'images/daycare_star_marker.png',
                                scaledSize: new google.maps.Size(70, 95), // Set size to 50x50 pixels
                            });
                        }
                    }
                }, 1000); // Adjust timeout as needed

            }, 500); // Adjust timeout as needed to ensure markers are added before fitting bounds
        },
        createMarker(position, center) {
            const contentString = `
          <div style="position: relative; width: 80px; height: 80px; border-radius: 30px; overflow: hidden;">
            <img src="${center.image}" style="width: 100%; height: 100%; object-fit: cover;">
          </div>
        `;

            const infowindow = new google.maps.InfoWindow({
                content: contentString,
            });

            const marker = new google.maps.Marker({
                position,
                map: this.map,
                title: center.name,
                icon: {
                    url: 'images/daycare_marker.png',
                    scaledSize: new google.maps.Size(50, 50),
                },
            });

            marker.addListener('click', () => {
                this.viewCenter(center);
            });

            this.markers.push(marker);
        },
        clearMarkers() {
            this.markers.forEach(marker => {
                marker.setMap(null);
            });
            this.markers = [];
        },
        calculateDistance(center) {
            // Haversine formula to calculate distance between two points in kilometers
            const R = 6371; // Radius of the Earth in km
            const lat1 = this.userLocation.lat;
            const lon1 = this.userLocation.lng;
            const lat2 = parseFloat(center.lat);
            const lon2 = parseFloat(center.lng);
            const dLat = this.deg2rad(lat2 - lat1);
            const dLon = this.deg2rad(lon2 - lon1);
            const a =
                Math.sin(dLat / 2) * Math.sin(dLat / 2) +
                Math.cos(this.deg2rad(lat1)) * Math.cos(this.deg2rad(lat2)) *
                Math.sin(dLon / 2) * Math.sin(dLon / 2);
            const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
            const distance = R * c; // Distance in km
            return distance;
        },
        deg2rad(deg) {
            return deg * (Math.PI / 180);
        },
    },
};
</script>

<style scoped>
.map {
    width: 100%;
    height: 400px;
}

.input-container {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.center-name,
.center-address,
.center-image {
    flex: 1;
    padding: 8px;
    box-sizing: border-box;
    margin-right: 5px;
}
</style>
