<template>
  <div class="pa-4 text-center">
    <v-dialog v-model="dialog" max-width="600">
      <template v-slot:activator="{ props: activatorProps }">
        <v-col cols="12" sm="6" md="4" lg="2"
          ><v-card
            rounded="xl"
            width="300"
            height="346"
            class="pa-5 ms-4"
            link
            hover
            align="center"
            color="blue-lighten-2"
            v-bind="activatorProps"
          >
            <v-row align="center" justify="center" class="h-100">
              <v-btn icon="mdi-plus" size="x-large" class="my-auto"></v-btn>
            </v-row> </v-card
        ></v-col>
      </template>
      <v-card flat class="pa-5">
        <v-card-title> Add New Guard </v-card-title>
        <v-text-field
          v-model="name"
          label="Name"
          hide-details
          required
          class="my-2"
        ></v-text-field>
        <v-select
          label="Is Parent"
          :items="parent"
          v-model="parentValue"
          class="mb-n3"
        ></v-select>
        <v-text-field
          v-model="mobile"
          label="Mobile"
          hide-details
          required
          class="my-2"
        ></v-text-field>

        <v-text-field
          v-model="phone"
          label="Phone"
          hide-details
          required
          class="my-2"
        ></v-text-field>

        <v-text-field
          v-model="email"
          label="E-mail"
          hide-details
          required
          class="my-2"
          @keydown.enter="addGuard()"
        ></v-text-field>

        <v-file-input
              v-model="image"
              prepend-icon=""
              label="Guard Picture"
              accept="image/*"
              color="deep-purple-accent-4"
              counter
            ></v-file-input>
        <v-row>
          <v-spacer> </v-spacer>
          <v-btn @click="addGuard()" class="ma-4" color="green"
            >Add Guard</v-btn
          >
        </v-row>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
export default {
  methods: {
    addGuard() {
      let data = {}
      if (this.image)
        data = {name : this.name, phone : this.phone, mobile: this.mobile, email: this.email, is_parent: this.parentValue === "Yes" ? true: false, image : this.image[0]}
      else
        data = {name : this.name, phone : this.phone, mobile: this.mobile, email: this.email, is_parent: this.parentValue === "Yes" ? true: false}
      this.$emit('addGuard', data);
      this.resetFields();
      this.dialog = false;
    },
    resetFields()
    {
      this.name = '';
      this.phone = '';
      this.email = '';
      this.mobile = '';
      this.parentValue = 'Yes';
      this.image = null;
    }
  },
  data: () => ({
    currentStep: 0,
    dialog: false,
    name: "",
    mobile: "",
    phone: "",
    email: "",
    parent: ["Yes", "No"],
    parentValue: "Yes",
    image : null,
  }),
};
</script>
