<template>
  <v-app-bar flat v-if="$route.name !== 'register'">
    <template v-slot:prepend>
      <v-app-bar-nav-icon @click="$store.state.drawer = !$store.state.drawer"></v-app-bar-nav-icon>
    </template>

    <v-app-bar-title>
      <div class="d-none d-sm-block">Daycare</div>
    </v-app-bar-title>
    <v-spacer></v-spacer>
    <!-- <v-text-field density="compact" variant="solo" label="Search For Center" append-inner-icon="mdi-magnify" single-line
      hide-details flat class="d-none d-md-block"></v-text-field>
    <v-spacer></v-spacer> -->

    <!-- <v-btn prepend-icon="mdi mdi-download-circle-outline" class="text-none">
      <template v-slot:prepend>
        <v-icon color="primary"></v-icon>
      </template>

      Download App
    </v-btn> -->
    
    <v-menu v-model="menu" :close-on-content-click="false" location="end" v-if="localStore">
      <template v-slot:activator="{ props }">
        <v-badge color="red" location="top end" :content="notificationUnreadCount" bordered  v-if="notificationUnreadCount > 0">
          <v-btn v-bind="props" icon="mdi-bell" variant="text" size="30">
          </v-btn>
        </v-badge>
        <v-btn v-bind="props" icon="mdi-bell" variant="text" size="30" v-else>
        </v-btn>
      </template>

      <v-card min-width="300">
        <p class="text-center py-1">Notifications</p>

        <v-divider></v-divider>

        <v-list>
          <v-list-item v-for="notification in notifications" :key="notification.id" :prepend-avatar="notification.image_path" 
          @click="navigateToUrl(notification)"
          :variant="notification.status === 'unread' ? 'tonal' : 'text'"
          :title="notification.title">
            <template v-slot:subtitle>
              <p class="notification-message">{{ notification.message }}</p>
              <p class="notification-message notification-time">{{ timeAgo(notification.created_at) }}</p>
            </template>
            <template v-slot:append>
            </template>
          </v-list-item>
        </v-list>

        <v-card-actions>
          <v-btn variant="text" color="primary" @click="markAllAsRead()">
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-menu>


    <v-btn @click="logout"> Logout </v-btn>
  </v-app-bar>
</template>

<script>
import { useAxios } from '@/composables/useAxios';

export default {
  async created()
  {
    if (localStorage.length > 0)
    {
      this.getNotifications();
    }
  },
  data()
  {
    return{
      fav: true,
      menu: false,
      message: false,
      hints: true,
      notificationUnreadCount: 0,
      notifications: [
      ]
    }
  },
  computed:{
    localStore()
    {
      return localStorage.length > 0;
    }
  },
  methods: {
    ...useAxios(),
    logout() {
      localStorage.clear();
      window.location.href = "/";
      
    },
    async getNotifications()
    {
      this.notifications = await this.get('notifications');
      this.notificationUnreadCount = this.notifications.filter((notification) => notification.status === 'unread').length;
    },
    async navigateToUrl(notification)
    {
      if (notification.child)
        {
          this.menu = false;
          await this.put('notifications/' + notification.id, {})
          if (notification.status == 'unread')
          {
            notification.status = 'read';
            this.notificationUnreadCount--;
          }
          this.$router.push({ name: 'child', params: { id: notification.child.id }, query: { activity: 'Supplies' } });
        }
    },
    async markAllAsRead(){
      const unreadNotifications = this.notifications.filter(notification => notification.status === 'unread');
    
      // If there are no unread notifications, return early
      if (unreadNotifications.length === 0) return;

      // Update the status locally
      unreadNotifications.forEach(notification => {
        notification.status = 'read';
      });

      await this.post('notifications/mark_all_as_read');
      this.notificationUnreadCount = 0;
    },
    timeAgo(date) {
      const now = new Date();
      const seconds = Math.floor((now - new Date(date)) / 1000);
      let interval = Math.floor(seconds / 31536000);

      if (interval >= 1) return interval === 1 ? "1 year ago" : `${interval} years ago`;
      interval = Math.floor(seconds / 2592000);
      if (interval >= 1) return interval === 1 ? "1 month ago" : `${interval} months ago`;
      interval = Math.floor(seconds / 86400);
      if (interval >= 1) return interval === 1 ? "1 day ago" : `${interval} days ago`;
      interval = Math.floor(seconds / 3600);
      if (interval >= 1) return interval === 1 ? "1 hour ago" : `${interval} hours ago`;
      interval = Math.floor(seconds / 60);
      if (interval >= 1) return interval === 1 ? "1 minute ago" : `${interval} minutes ago`;
      return "Just now";
    },
  },
};
</script>

<style scoped>
.notification-message
{
  font-size: 0.8rem;
  max-width: 300px;
  white-space: normal; 
  overflow: hidden; 
  text-overflow: ellipsis;
  word-break: break-word;
}
.notification-time
{
  font-size: 0.8rem;
  font-weight: bold;
}

</style>
