<template>
  <div class="pa-4 text-center">
    <v-dialog v-model="$store.state.childNoteDialog" max-width="600">
      <template v-slot:activator="{ props: activatorProps }">
        <v-btn
          class="text-none font-weight-regular"
          prepend-icon="mdi-plus"
          text="Add Note"
          variant="text"
          color="green"
          v-bind="activatorProps"
        ></v-btn>
      </template>

      <v-card prepend-icon="mdi-plus" title="Add Note">
        <v-card-text>
          <v-row dense>
            <v-col cols="12" class="mt-2">
              <v-text-field
                v-model="$store.state.childNoteTitle"
                label="Title*"
                required
              ></v-text-field>
            </v-col>

            <v-col cols="12">
              <v-text-field
                v-model="$store.state.childNoteDescription"
                label="Description*"
              ></v-text-field>
            </v-col>
          </v-row>

          <small class="text-caption text-medium-emphasis"
            >*indicates required field</small
          >
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            text="Close"
            variant="plain"
            @click="$store.state.childNoteDialog = false"
          ></v-btn>

          <v-btn
            color="primary"
            text="Save"
            variant="tonal"
            @click="addChildNote()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
export default {
  methods: {
    addChildNote() {
      this.$store.state.childNoteDialog = false;
    },
  },
  data: () => ({}),
};
</script>
