<script>
import axios from "axios";
import ViewCenterDialog from "@/components/home/ViewCenterDialog.vue";
import TourController from '@/components/TourController.vue';
import { mapGetters } from 'vuex';
import introJs from 'intro.js';
import Map from "@/components/global/GoogleMap.vue";
import { useAxios } from "@/composables/useAxios";

export default {
  async created()
  {
    await this.getCenters();
  },
  mounted() {
    // this.$store.state.selectedCenter = this.daycare_list[0];
    // this.$store.state.viewCenterDialog = true

  },
  methods:
  {
    ...useAxios(),
    showMapDialog()
    {
      const address = this.$store.state.userInfo.profile.address;
      if (!address || address == '')
      {
        this.$store.dispatch("setAlert", {
            type: "error",
            title: "",
            text: "You must update your address to be able to use this feature!",
          }); 
        return;
      }
      this.$store.state.mapDialog = true;
    },
    async getCenters() {
      this.daycare_list = await this.get('business-units');
    },
    async handleViewCenterFromMap(center) {
      this.viewCenterData(center);
    },
    async viewCenterData(center) {
      if (this.$store.state.children.children.length < 1)
        this.$store.state.children.children = await this.get('children');
      this.$store.dispatch('tour/refreshIntro');
      this.$store.state.selectedCenter = center;
      this.$store.state.viewCenterDialog = true
    },
    startTour() {
      this.$nextTick(() => {
        introJs().setOptions({ steps: this.tourSteps }).start();
      });
    },
    filterChanged(g_item) {
      this.filterOptions.forEach(item => {
        if (item != g_item)
          item.state = false;
      })
    },
    haversineDistance(lat1, lon1, lat2, lon2) {
      const toRadians = degree => degree * Math.PI / 180;
      const R = 6371; // Radius of the Earth in kilometers

      const dLat = toRadians(lat2 - lat1);
      const dLon = toRadians(lon2 - lon1);

      const a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(toRadians(lat1)) * Math.cos(toRadians(lat2)) *
        Math.sin(dLon / 2) * Math.sin(dLon / 2);

      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
      return R * c; // Distance in kilometers
    },
    sortByNearestCenters(daycareList, myLat, myLon) {
      return daycareList.sort((a, b) => {
        const distanceA = this.haversineDistance(myLat, myLon, a.latitude, a.longitude);
        const distanceB = this.haversineDistance(myLat, myLon, b.latitude, b.longitude);
        return distanceA - distanceB;
      });
    }
  },
  computed: {
    ...mapGetters('tour', ['tourSteps', 'isTourActive']),
  },
  isTourActive(newVal) {
    console.log('isTourActive changed:', newVal); // Add this line for debugging
    if (newVal) {
      this.startTour();
    }
  },
  components:
  {
    ViewCenterDialog,
    TourController,
    Map

  },
  data() {
    return {
      images : [
          'https://www.greatschools.org/gk/wp-content/uploads/2014/03/The-school-visit-what-to-look-for-what-to-ask-1.jpg',
          'https://static01.nyt.com/images/2023/09/28/multimedia/00NAT-DOD-SCHOOLS-vjwl/00NAT-DOD-SCHOOLS-vjwl-superJumbo.jpg',
          'https://upload.wikimedia.org/wikipedia/commons/f/f1/School-education-learning-1750587-h.jpg',
          'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQhI6rOTL17Oji2sVwE8KUEJ6TGLicjcAwHBvyFHOPTwQ&s',
        ],
      progress_value: 30,
      filteredCenterName: "",
      dialog: true,
      daycare_list: [
      
      ],
      filterOptions: [
        { title: "Nearst to me", value: 0, state: false },
        { title: "Less price to high price", value: 1, state: false },
        { title: "High price to less price", value: 2, state: false },
      ],
    };
  },
  computed: {
    filteredData() {
      if (this.filteredCenterName) {
        const searchWords = this.filteredCenterName.toLowerCase().split(" ");
        return this.daycare_list.filter((center) => {
          return searchWords.every((word) =>
            center.name.toLowerCase().includes(word)
          );
        });
      }
      if (this.filterOptions[0].state) // Nearst to me activated
      {
        console.log(this.daycare_list);
        const sortedDaycareList = this.sortByNearestCenters(this.daycare_list, this.$store.state.latitude, this.$store.state.longitude);
        console.log(sortedDaycareList);
        return sortedDaycareList;
      }
      return this.daycare_list;

    },
  },
};
</script>

