<template>
    <v-container>
        <VRow>
            <Search />
        </VRow>

        <!-- Second Row -->
        <v-row align="center mt-10">
            <VCol align="center">
                <v-chip color="primary" label size="large">
                    Question
                </v-chip>
                <p class="text-h4 mt-3 font-wight-bold">You still have a question?</p>
                <p class="mt-3">If you can't find question in our FAQ, you can contact us. We'll answer you shortly!</p>
            </VCol>
        </v-row>

        <!-- Third Row -->
        <v-row align="center" justify="space-between">
            <v-col cols="6" align="center" class="contact  rounded-lg" v-for="contact in contacts">
                <v-card class='bg-teal-lighten-5'>
                    <v-avatar color="blue-darken-2 rounded-lg mt-5">
                        <v-icon :icon="contact.icon"></v-icon>
                    </v-avatar>
                    <p class="mt-3 text-title"> {{ contact.contact }}</p>
                    <p class="mt-3 font-weight-light text-subtitle-1"> {{ contact.desc }}</p>
                </v-card>
            </v-col>
        </v-row>

        <v-row>
            <v-col>
                <h2>Frequently Asked Questions</h2>
                <v-expansion-panels class="my-4" variant="inset">
                    <v-expansion-panel v-for="(faq, index) in fqas" :key="index" :text="faq.answer"
                        :title="faq.question">
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-col>
        </v-row>
        <v-row style="height:300px"</v-row>
    </v-container>
</template>



<script>
import Search from '@/components/faq/Search.vue'
import { useAxios } from '@/composables/useAxios';
export default {
    data() {
        return {
            contacts: [
                { icon: 'mdi-phone', contact: '+ (810) 2548 2568', desc: 'We are always happy to help!' },
                { icon: 'mdi-mail', contact: 'support@daycare.com', desc: 'Best way to get answer faster!' },
            ],
            fqas: [
              
            ]


        }
    },
    components:
    {
        Search,
    },
    created()
    {
        this.getFQA();
    },
    methods:
    {
        ...useAxios(),
        async getFQA()
        {
            this.fqas = await this.get('fqa');

        }
    }

}
</script>

<style scoped>
p {
    font-size: 20px;
}

.contact {
    height: 300px;
}
</style>