<template>
  <v-card class="mx-auto">
    <v-row style="height: 70px" align="center">
      <v-spacer></v-spacer>
      <medical-dialog
        :value="dialogOpen"
        :medicalRecord="selectedMedicalRecord"
        @input="dialogOpen = $event"
        @save="handleSave"
      ></medical-dialog>
    </v-row>
    <TransitionGroup name="fade" class="container">
      <v-list lines="three">
        <v-list-item
          v-for="record in medicalRecords"
          :key="record.id"
          :title="record.situation"
          :subtitle="record.notes"
          :value="record.id"
          prepend-avatar="/images/medical.png"
          @click="editRecord(record)"
        >
          <template v-slot:append>
            <v-btn color="green" icon="mdi-pen" variant="text" @click.stop="editRecord(record)"></v-btn>
            <v-btn color="red" icon="mdi-delete" variant="text" @click.stop="deleteRecord(record)"></v-btn>
          </template>
        </v-list-item>
      </v-list>
    </TransitionGroup>
  </v-card>
</template>

<script>
import MedicalDialog from './MedicalDialog.vue';
import { useAxios } from '@/composables/useAxios';
export default {
  components: {
    MedicalDialog,
  },
  data() {
    return {
      childId: null,
      dialogOpen: false,
      medicalRecords: [],
      selectedMedicalRecord: {
        situation: '',
        notes: '',
      },
    };
  },
  created() {
    this.childId = this.$route.params.id;
    this.fetchMedicalRecords();
  },
  methods: {
    ...useAxios(),
    async fetchMedicalRecords() {
      this.medicalRecords = await this.get('children/' + this.childId + '/medical-records')
    },
    async deleteRecord(item) {
      await this.del('children/' + this.childId + '/medical-records/' + item.id) 
      await this.fetchMedicalRecords();
    },
    editRecord(item) {
      this.selectedMedicalRecord = { ...item };
      this.dialogOpen = true;
    },
    handleSave(data) {
      const isEditing = !!this.selectedMedicalRecord.id;
      if (isEditing) {
        this.updateMedicalRecord(data);
      } else {
        this.addNewMedicalRecord(data);
      }
    },
    async addNewMedicalRecord(data) {
      const situation = data.situation;
      const notes = data.notes;
      const need_treatment = data.needTreatment;
      const dosage = data.dosage;
      const times = data.times;
      await this.post('children/' + this.childId + '/medical-records', {situation, notes, need_treatment, dosage, times})
      await this.fetchMedicalRecords();
    },
    async updateMedicalRecord(data) {
      const situation = data.situation;
      const notes = data.notes;
      const need_treatment = data.needTreatment;
      const dosage = data.dosage;
      const times = data.times;
      await this.put('children/' + this.childId + '/medical-records/' + this.selectedMedicalRecord.id, {situation, notes, need_treatment, dosage, times})
      await this.fetchMedicalRecords();
    },
  },
};
</script>
