<template>
  <v-container fluid>
    <v-row justify="center" align="center">
      <TransitionGroup name="fade" class="container">
        <v-col
          align="center"
          v-for="guard in guardians"
          :key="guard.id"
          cols="12"
          sm="6"
          md="6"
          lg="4"
          xl="2"
        >
          <v-card rounded="xl" class="pa-5" link hover>
            <v-card-title align="center">
              <v-avatar
                class="elevation-10"
                :image="guard.image ? guard.image : 'https://cdn-icons-png.flaticon.com/512/3001/3001764.png'"
                size="100"
              ></v-avatar>
              <p class="text-blue-darken-4 text-h5 mt-3">{{ guard.name }}</p>
            </v-card-title>
            <v-card-action align="center" class="mt-2">
              <div class="my-4 d-flex align-center gap-2">
                <div class="text-center w-100">
                  <p class="mb-1">Gender</p>
                </div>
                <hr
                  class="v-divider v-divider--vertical v-theme--DefaultTheme"
                  aria-orientation="vertical"
                  role="separator"
                />
                <div class="text-center w-100">
                  <p class="mb-1">
                    {{ guard.is_parent ? "Parent" : "Not Parent" }}
                  </p>
                </div>
                <hr
                  class="v-divider v-divider--vertical v-theme--DefaultTheme"
                  aria-orientation="vertical"
                  role="separator"
                />
                <div class="text-center w-100">
                  <p class="mb-1">Other</p>
                </div>
              </div>
              <v-divider class="my-1"></v-divider>
              <div class="d-flex align-center gap-2">
                <div class="text-center w-100">
                  <v-btn
                    icon="mdi-email"
                    size="small"
                    variant="text"
                    class="text-primary"
                    :href="`mailto:${guard.email}`"
                  ></v-btn>
                </div>
                <hr
                  class="v-divider v-divider--vertical v-theme--DefaultTheme"
                  aria-orientation="vertical"
                  role="separator"
                />
                <div class="text-center w-100">
                  <v-btn
                    icon="mdi-phone"
                    size="small"
                    variant="text"
                    class="text-primary"
                    :href="`tel:${guard.mobile}`"
                  ></v-btn>
                </div>
              </div>
              <v-divider class="my-2"></v-divider>
              <v-btn
                class="text-none text-red"
                variant="plain"
                @click.stop="deleteGuard(guard.id)"
                >Delete
                <template v-slot:prepend>
                  <v-icon class="text-red">mdi-delete</v-icon>
                </template>
              </v-btn>
            </v-card-action>
          </v-card>
        </v-col>
        <add-guard-dialog @addGuard="addGuard" />
      </TransitionGroup>
    </v-row>
  </v-container>
</template>

<script>
import AddGuardDialog from "./AddGuardDialog.vue";
import { useAxios } from "@/composables/useAxios";
export default {
  async created() {
    this.childId = this.$route.params.id;
    await this.fetchGuardians();
  },
  components: {
    AddGuardDialog,
  },
  methods: {
    ...useAxios(),
    async deleteGuard(id) {
      await this.del('children/' + this.childId + '/guardians/' + id)
      await this.fetchGuardians();
    },
    async fetchGuardians(guards) {
      this.guardians = await this.get('children/' + this.childId + '/guardians')
    },
    async addGuard(guard) {
      const formData = new FormData();
      Object.keys(guard).forEach(key => {
        formData.append(key, guard[key]);
      })
      await this.post('children/' + this.childId + '/guardians', formData, {'Content-Type': 'multipart/form-data'});
      await this.fetchGuardians();
    },
  },
  data() {
    return {
      guardians: [],
      childId : null,
    };
  },
};
</script>
