<template>
  <div class="pa-4 text-center">
    <v-dialog v-model="dialog" max-width="600">
      <template v-slot:activator="{ props: activatorProps }">
        <v-btn
          class="text-none font-weight-regular"
          prepend-icon="mdi-plus"
          text="Add Document"
          variant="text"
          color="green"
          v-bind="activatorProps"
        ></v-btn>
      </template>

      <v-card prepend-icon="mdi-plus" title="Add Document">
        <v-card-text>
          <v-row dense>
            <v-col cols="12" class="mt-2">
              <v-text-field
                v-model="name"
                label="Title*"
                required
              ></v-text-field>
            </v-col>

            <v-col cols="12">
              <v-file-input v-model="document" label="document Upload"></v-file-input>
            </v-col>
          </v-row>

          <small class="text-caption text-medium-emphasis"
            >*indicates required field</small
          >
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn text="Close" variant="plain" @click="dialog = false"></v-btn>

          <v-btn
            color="primary"
            text="Save"
            variant="tonal"
            @click="addChildNote()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
export default {
  methods: {
    addChildNote() {
      this.dialog = false;
      const data = {name : this.name, file: this.document[0]};
      this.$emit('addDocument', data);
      this.resetFields();
    },
    resetFields()
    {
      this.name = '';
      this.document = null;
    }
    
  },
  data: () => ({
    dialog: false,
    name : '',
    document : null,
  }),
};
</script>
