<template>
  <v-card class="mx-auto">
    <v-row style="height: 70px" align="center">
      <v-spacer></v-spacer>
      <subsidy-dialog :value="dialogOpen" :subsidy="selectedSubsidy" @input="dialogOpen = $event"
        @save="handleSave"></subsidy-dialog>
    </v-row>
    <TransitionGroup name="fade" class="container">
      <v-list lines="three">
        <v-list-item v-for="subsidy in subsidies" :key="subsidy.id"
          @click="editSubsidy(subsidy)"
          :title="'Discount Value: ' + subsidy.discount_ratio + '%'"
          :subtitle="subsidy.start_date + '  :  ' + subsidy.end_date" :value="subsidy.id"
          prepend-avatar="/images/percentage.png">
          <template v-slot:append>
            <v-btn color="green" icon="mdi-pen" variant="text" @click.stop="editSubsidy(subsidy)"></v-btn>
            <v-btn color="red" icon="mdi-delete" variant="text" @click.stop="deleteSubsidy(subsidy)"></v-btn>
          </template>
        </v-list-item>
      </v-list>
    </TransitionGroup>
  </v-card>
</template>

<script>
import SubsidyDialog from './SubsidyDialog.vue';
import { useAxios } from '@/composables/useAxios';
export default {
  components: {
    SubsidyDialog,
  },
  data() {
    return {
      childId: null,
      dialogOpen: false,
      subsidies: [],
      selectedSubsidy: {
        id: null,
        start_date: '',
        end_date: '',
        DiscountRatio: '',
        document: null,
        documentName: null,
      },
    };
  },
  async created() {
    this.childId = this.$route.params.id;
    await this.fetchSubsidies();
  },
  methods: {
    ...useAxios(),
    async fetchSubsidies() {
      this.subsidies = await this.get('children/' + this.childId + '/subsidies')
    },
    async deleteSubsidy(item) {
      await this.del('children/' + this.childId + '/subsidies/' + item.id)
      this.fetchSubsidies();
    },
    editSubsidy(item) {
      this.selectedSubsidy = { ...item };
      this.dialogOpen = true;
    },
    handleSave(data) {
      const isEditing = !!this.selectedSubsidy.id;
      if (isEditing) {
        this.updateSubsidy(data);
      } else {
        this.addNewSubsidy(data);
      }
    },
    async addNewSubsidy(data) {
      data.document = data.document[0];
      const formData = new FormData();
      Object.keys(data).forEach(key => {
          formData.append(key, data[key]);
      });
      await this.post('children/' + this.childId + '/subsidies', formData, {'Content-Type': 'multipart/form-data',});
      this.fetchSubsidies();
    },
    async updateSubsidy(data) {
      await this.put('children/' + this.childId + '/subsidies/' + this.selectedSubsidy.id, data);
      this.fetchSubsidies();
    },
  },
};
</script>
