import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/HomeView.vue";
import SocialView from "../views/SocialView.vue";
import ChildrenView from "../views/ChildrenView.vue";
import ChildView from "../views/ChildView.vue";
import RegisterView from "../views/RegisterView.vue";
import MessagesVue from "../views/MessagesView.vue";
import ActivitiesView from "../views/ActivitiesView.vue";
import ChildActivitiesView from "../views/ChildActivitiesView.vue";
import FQAView from "../views/FQAView.vue"
const routes = [
  {
    path: "/",
    name: "register",
    component: RegisterView,
  },
  {
    path: "/home",
    name: "home",
    component: HomeView,
  },
  {
    path: "/social",
    name: "social",
    component: SocialView,
  },
  {
    path: "/children",
    name: "children",
    component: ChildrenView,
  },
  {
    path: "/child/:id",
    name: "child",
    component: ChildView,
  },
  {
    path: "/activities",
    name: "activities",
    component: ActivitiesView,
  },
  {
    path: "/activities/child/:id",
    name: "child_activities",
    component: ChildActivitiesView,
  },
  {
    path: "/messages",
    name: "messages",
    component: MessagesVue,
  },
  {
    path: "/profile",
    name: "profile",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/ProfileView.vue"),
  },
  {
    path: '/fqa',
    name: "fqa",
    component: FQAView
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
