<template>
  <div class="pa-2">
    <NavDrawer :conversations="conversations" @add-conversation="addConversation"
      @getConversationMessages="getConversationMessages" />
    <Appbar :conversation="selectedConversation" v-if="selectedConversation" />
    <v-main class="pa-0" style="padding-top: 100px; padding-bottom: 64px">
      <v-row align="start">
        <v-col>
          <section class="chat mt-3">
            <div class="messages-chat" ref="messagesContainer">
              <div v-for="(message, index) in messages" :key="index">
                <div class="message" v-if="
                  !message.me &&
                  (index === messages.length - 1 ||
                    messages[index + 1].sender !== message.sender)
                ">
                  <div v-if="!message.me" class="photo" :style="{ backgroundImage: 'url(' + message.image + ')' }">
                    <div class="online"></div>
                  </div>
                  <p class="text" v-if="!message.me" v-html="message.text"></p>
                </div>
                <div class="message text-only" v-else>
                  <p class="text" v-if="!message.me">{{ message.text }}</p>
                </div>
                <!-- Respond -->
                <div class="message text-only">
                  <div class="response" v-if="message.me">
                    <p class="text">{{ message.text }}</p>
                  </div>
                </div>

                <template v-if="
                  !message.me &&
                  (index === messages.length - 1 ||
                    messages[index + 1].sender !== message.sender)
                ">
                  <p class="time">{{ message.time }}</p>
                </template>
                <template v-if="
                  message.me &&
                  (index === messages.length - 1 ||
                    messages[index + 1].sender !== message.sender)
                ">
                  <p class="response-time time">{{ message.time }}</p>
                  <br />
                </template>
              </div>
            </div>
          </section>
        </v-col>
      </v-row>
    </v-main>
    <v-app-bar location="bottom" flat style="border-top: 0.1px solid #e7dbdb8a" class="pa-2">
      <v-text-field density="compact" variant="solo" rounded="xxl" label="Type your message..." single-line hide-details
        flat class="d-none d-md-block" v-model="text" @keydown.enter="sendMessage">
        <template v-slot:append-inner>
          <v-btn icon="mdi-attachment"></v-btn>
          <v-btn append-icon="mdi-send" color="#5865f2" variant="flat" class="text-none"
            @click="sendMessage()">Send</v-btn>
        </template>
      </v-text-field>
    </v-app-bar>
  </div>
</template>

<script>
import NavDrawer from "../components/messages/NavDrawer.vue";
import Appbar from "../components/messages/Appbar.vue";
import { useAxios } from "@/composables/useAxios";
export default {
  async created() {
    await this.getConversations();
    const conversationParam = this.$route.query.c; //this.$route.query.c
    if (conversationParam)
    {
      const conversationIndex = this.conversations.findIndex(conversation =>
      {
        return conversation.channelID === conversationParam
      });
      
      if (conversationIndex)
        this.selectedConversation = this.conversations[conversationIndex];
      else
        this.selectedConversation = this.conversations[0];
    }else
    {
      this.selectedConversation = this.conversations[0];
    }
    
    
    if (this.selectedConversation)
      await this.getMessages(this.selectedConversation.channelID);

    this.$nextTick(() => {
      this.scrollToBottom(false);
    });
  },
  methods: {
    ...useAxios(),
    async connectConversation(conversationId) {
      const token = localStorage.getItem("token");

      if (this.socket) {
        this.socket.close();
      }

      this.setupSocket(conversationId, token);
    },

    setupSocket(conversationId, token) {
      this.socket = new WebSocket(`ws://localhost:8000/ws/conversation/${conversationId}/?token=${token}`);

      this.socket.onopen = (e) => {
        console.log('WebSocket connection established');
        this.reconnectAttempts = 0; // Reset reconnect attempts on successful connection
        this.reconnectDelay = 1000; // Reset delay
      };

      this.socket.onclose = (e) => {
        console.log('WebSocket connection closed');
      };

      this.socket.onmessage = (e) => {
        const data = JSON.parse(e.data);
        const message = {
          id: data.id,
          sender: data.sender_object_id,
          image: data.image,
          text: data.body,
          time: data.time,
          me: data.sender_object_id === this.$store.state.userInfo.parent.id,
        };
        this.writeMessage(message, true);

        if (!message.me) {
          this.audio.play();
        }
      };

      this.socket.onerror = (error) => {
        console.error('WebSocket error:', error);
      };
    },
    async getConversations() {
      const conversations = await this.get('conversations', { last: true, participants: false })
      conversations.forEach(conversation => {
        const conv = {
          channelID: conversation.id,
          id: conversation.id,
          value: conversation.id,
          name: conversation.contact.name,
          contact_id: conversation.contact.object_id,
          image: conversation.contact.image,
          role: conversation.contact.content_type_name,
          lastMessage: conversation.messages ? conversation.messages.body : '',
          selected: true,
        };
        this.conversations.push(conv);
      });
    },
    async getMessages(channelID) {
      this.messages = [];
      const messages = await this.get('conversations/' + channelID + "/messages")
      messages.forEach(element => {
        const message = {
          id: element['id'],
          sender: element['sender_object_id'],
          image: element['image'],
          text: element['body'],
          time: element['time'],
          me: element['sender_object_id'] === this.$store.state.userInfo.parent.id
        }
        this.messages.push(message);
      });
      this.$nextTick(() => {
      this.scrollToBottom(false);
      }); 
      this.connectConversation(channelID);
    },
    async getConversationMessages(conversation) {
      this.$store.state.messagesDrawer = false;
      this.selectedConversation = conversation;
      await this.getMessages(this.selectedConversation.channelID);

    },
    async reconnectSocket() {
      if (this.reconnectAttempts >= this.maxReconnectAttempts) {
        console.error('Max reconnection attempts reached');
        return false;
      }

      await new Promise(resolve => setTimeout(resolve, this.reconnectDelay));
      
      this.reconnectAttempts++;
      this.reconnectDelay *= 2; // Exponential backoff
      
      const conversationId = this.selectedConversation.id; // Assuming you're using vue-router
      const token = localStorage.getItem("token");
      
      this.setupSocket(conversationId, token);
      
      return true;
    },

    async sendMessage() {
      if (this.text.trim() === "") {
        return;
      }

      try {
        // Check if socket is closed or closing
        if (!this.socket || this.socket.readyState === WebSocket.CLOSED || this.socket.readyState === WebSocket.CLOSING) {
          console.log('Socket is closed, attempting to reconnect...');
          const reconnected = await this.reconnectSocket();
          if (!reconnected) {
            console.error('Failed to reconnect socket');
            // You might want to show an error message to the user here
            return;
          }
          
          // Wait for the socket to be ready
          await new Promise(resolve => {
            const checkSocket = setInterval(() => {
              if (this.socket.readyState === WebSocket.OPEN) {
                clearInterval(checkSocket);
                resolve();
              }
            }, 100);
          });
        }

        this.socket.send(JSON.stringify({
          'type': 'send-message',
          'message': this.text,
        }));

        this.text = "";
      } catch (error) {
        console.error('Error sending message:', error);
        // You might want to show an error message to the user here
      }
    },
    async writeMessage(message, is_add) {
      this.messages.push(message);
      this.$nextTick(() => {
        this.scrollToBottom(is_add);
      });
    },
    scrollToBottom(add) {
    const container = this.$refs.messagesContainer;
    if (container) {  // Ensure the container exists
        const firstMessage = container.firstElementChild;
        const lastMessage = container.lastElementChild;
        if (firstMessage && !add) {
            firstMessage.scrollIntoView();
        }
        if (lastMessage) {
            lastMessage.scrollIntoView({ behavior: "smooth" });
        }
    } else {
        console.warn("Messages container not found.");
    }
    },
    async addConversation(newConversation) {
      const conversationIndex = this.conversations.findIndex(conversation =>
      {
        console.log(conversation.contact_id)
        console.log(newConversation.id)
        return conversation.contact_id === newConversation.id
      });
      if (conversationIndex === -1) {
        const data = { contact: {id : newConversation.id, type: newConversation.type} };
        const response = await this.post('conversations', data);
        // const channelID = await this.$store.dispatch('messages/setMessageChannel', { firstId: newConversation.id, firstType: newConversation.terminalType, allowReply: true });
        newConversation.channelID = response.id;
        newConversation.contact_id = response.contact.object_id;
        this.conversations.unshift(newConversation);
      } else {
        const existingConversation = this.conversations.splice(conversationIndex, 1)[0];
        this.conversations.unshift(existingConversation);
      }
      this.getMessages(this.conversations[0].channelID);
      // console.log("Messages: ", messages)
    }
  },
  mounted()
  {
    this.audio = new Audio('/chat-sound.mp3');
  },
  data() {
    return {
      audio : null,
      socket: null,
      selectedConversation: null,
      text: "",
      conversations: [

      ],
      messages: [

      ],
    };
  },
  components: {
    NavDrawer,
    Appbar,
  },
};
</script>

<style>
.header-chat {
  background-color: #fff;
  height: 90px;
  box-shadow: 0px 3px 2px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
}

.chat .header-chat .icon {
  margin-left: 30px;
  color: #515151;
  font-size: 14pt;
}

.chat .header-chat .name {
  margin: 0 0 0 20px;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-size: 13pt;
  color: #515151;
}

.chat .header-chat .right {
  position: absolute;
  right: 40px;
}

.chat .messages-chat {
  padding: 25px 64px;
}

.chat .messages-chat .message {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}

.chat .messages-chat .message .photo {
  display: block;
  width: 45px;
  height: 45px;
  background: #e6e7ed;
  -moz-border-radius: 50px;
  -webkit-border-radius: 50px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.chat .messages-chat .text {
  margin: 0 35px;
  background-color: #f6f6f6;
  padding: 15px;
  border-radius: 12px;
}

.text-only {
  margin-left: 45px;
}

.time {
  font-size: 10px;
  color: lightgrey;
  margin-bottom: 10px;
  margin-left: 85px;
}

.response-time {
  float: right;
  margin-right: 40px !important;
}

.response {
  float: right;
  margin-right: 0px !important;
  margin-left: auto;
  /* flexbox alignment rule */
}

.response .text {
  background-color: #e3effd !important;
}

.footer-chat {
  width: calc(65% - 66px);
  height: 80px;
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 0;
  background-color: transparent;
  border-top: 2px solid #eee;
}

.chat .footer-chat .icon {
  margin-left: 30px;
  color: #c0c0c0;
  font-size: 14pt;
}

.chat .footer-chat .send {
  color: #fff;
  background-color: #4f6ebd;
  position: absolute;
  right: 50px;
  padding: 12px 12px 12px 12px;
  border-radius: 50px;
  font-size: 14pt;
}

.chat .footer-chat .name {
  margin: 0 0 0 20px;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-size: 13pt;
  color: #515151;
}

.chat .footer-chat .right {
  position: absolute;
  right: 40px;
}

.write-message {
  border: none !important;
  width: 60%;
  height: 50px;
  margin-left: 20px;
  padding: 10px;
}

.footer-chat *::-webkit-input-placeholder {
  color: #c0c0c0;
  font-size: 13pt;
}

.footer-chat input *:-moz-placeholder {
  color: #c0c0c0;
  font-size: 13pt;
}

.footer-chat input *::-moz-placeholder {
  color: #c0c0c0;
  font-size: 13pt;
  margin-left: 5px;
}

.footer-chat input *:-ms-input-placeholder {
  color: #c0c0c0;
  font-size: 13pt;
}

.clickable {
  cursor: pointer;
}
</style>
