<template>
    <v-card class="mx-auto">
      <v-row style="height: 70px" align="center">
        <v-spacer></v-spacer>
      </v-row>
      <TransitionGroup name="fade" class="container">
        <v-list lines="three">
          <v-list-item
            v-for="record in admissionRequests"
            :key="record.id"
            :title="record.business_unit_name"
            :subtitle="record.type_name"
            :value="record.id"
            prepend-avatar="/images/medical.png"
          >
            <template v-slot:append>
              <v-btn v-if="record.type == 'under_review'" color="red" icon="mdi-delete" variant="text" @click.stop="deleteRecord(record)"></v-btn>
            </template>
          </v-list-item>
        </v-list>
      </TransitionGroup>
    </v-card>
  </template>
  
  <script>
  import { useAxios } from '@/composables/useAxios';
  export default {
    data() {
      return {
        childId: null,
        dialogOpen: false,
        admissionRequests: [],
        selectedMedicalRecord: {
          situation: '',
          notes: '',
        },
      };
    },
    created() {
      this.childId = this.$route.params.id;
      this.fetchAdmissionRequests();
    },
    methods: {
      ...useAxios(),
      async fetchAdmissionRequests() {
        this.admissionRequests = await this.get('children/' + this.childId + '/admission-requests')
      },
      async deleteRecord(item) {
        await this.del('children/' + this.childId + '/admission-requests/' + item.id) 
        await this.fetchAdmissionRequests();
      },
    },
  };
  </script>
  