const state = {
    businessUnits: []
};

const mutations = {

};

const actions = {
    async getParentBusinessUnits({ dispatch, commit }) {
        try {
            const url = "GetPage";
            const controller = "GetParentBusinessUnitsList";
            const response = await dispatch("makeApiRequest", { 
                url,
                controller,
            }, { root: true });  
            
            let businessUnits = response.data;
            const parsedData = await dispatch('parseData', {jsonData: businessUnits}, {root: true});
            
            businessUnits = parsedData;
            // commit('setBusinessUnits', businessUnits); // Optionally commit the business units to the state
            return businessUnits;
            
        } catch (error) {
            console.error("API Request Error:", error);
            return []; // Return an empty array in case of error
        }
    },
    async getParentClassRooms({ dispatch, commit }) {
        try {
            const url = "GetPage";
            const controller = "GetParentClassRooms";
            const response = await dispatch("makeApiRequest", { 
                url,
                controller,
            }, { root: true });  
            
            let classRooms = response.data;
            const parsedData = await dispatch('parseData', {jsonData: classRooms}, {root: true});
            
            classRooms = parsedData;
            return classRooms;
            
        } catch (error) {
            console.error("API Request Error:", error);
            return []; // Return an empty array in case of error
        }
    },
    async getParentTeachers({ dispatch, commit },) {
        try {
            const url = "GetPage";
            const controller = "GetParentTeachers";
            const response = await dispatch("makeApiRequest", { 
                url,
                controller,
            }, { root: true });  
            
            let teachers = response.data;
            const parsedData = await dispatch('parseData', {jsonData: teachers}, {root: true});
            
            teachers = parsedData;
            return teachers;
            
        } catch (error) {
            console.error("API Request Error:", error);
            return []; // Return an empty array in case of error
        }
    },
    async setMessageChannel({ dispatch, commit }, { firstId, firstType, allowReply }) {
        const controller = "MessageChannels";
        const Values = [
            { Name: "FirstTerminalID", NewValue: firstId, Modified: true },
            { Name: "FirstTerminalTypeID", NewValue: firstType, Modified: true },
            { Name: "SecondTerminalTypeID", NewValue: 7, Modified: true }, // 7 is parent terminal Type
            { Name: "AllowReply", NewValue: allowReply, Modified: true },
        ];
        try {
            const response = await dispatch("InsertRequest", { controller, Values }, { root: true });
            let channelID = null;
            if (response.d.Errors.length > 0)
            {
                const channel = await dispatch('getMessageChannel', {firstId, firstType, allowReply})
                channelID = channel['MESSAGE_CHANNEL_ID'];
            }else
                channelID = response.d.Values[0].NewValue;
            //dispatch("setAlert", { type: "success", title: "Message Channel", text: "New Message Channel Created" }, { root: true });
            return channelID; // Ensure the channelID is returned
        } catch (error) {
            dispatch("setAlert", { type: "error", title: "Message Channel", text: error }, { root: true });
        }
    },
    async getMessageChannel({ dispatch, commit }, { firstId, firstType, allowReply}) {
        try {
            const url = "GetPage";
            const controller = "UserMessageChannels";
            const filter = ["FIRST_TERMINAL_ID:=" + firstId, "FIRST_TERMINAL_TYPE_ID:=" + firstType, "ALLOW_REPLY:=" + allowReply];
            const response = await dispatch("makeApiRequest", { 
                url,
                controller,
                filter,
            }, { root: true });  
            
            let channels = response.data;
            const parsedData = await dispatch('parseData', {jsonData: channels}, {root: true});  
            channels = parsedData;
            console.log("CHANNELS", channels);
            return channels[0];        
        } catch (error) {
            console.error("API Request Error:", error);
            return []; // Return an empty array in case of error
        }
    },
    async getMessageChannels({ dispatch, commit }) {
        try {
            const url = "GetPage";
            const controller = "UserMessageChannels";
            const response = await dispatch("makeApiRequest", { 
                url,
                controller,
            }, { root: true });  
            
            let channels = response.data;
            const parsedData = await dispatch('parseData', {jsonData: channels}, {root: true});  
            channels = parsedData;
            return channels;        
        } catch (error) {
            console.error("API Request Error:", error);
            return []; // Return an empty array in case of error
        }
    },
    async getMessages({ dispatch, commit },{channelID}) {
        try {
            const url = "GetPage";
            const filter = ["MessageChannelID:=" + channelID];
            const controller = "OrgMessages";
            const request = { SortExpression : 'SendDatetime asc'};
            const response = await dispatch("makeApiRequest", { 
                url,
                controller,
                filter,
                request,
            }, { root: true });  
            
            let messages = response.data;
            const parsedData = await dispatch('parseData', {jsonData: messages}, {root: true});  
            messages = parsedData;
            return messages;
            
        } catch (error) {
            console.error("API Request Error:", error);
            return []; // Return an empty array in case of error
        }
    },
    async sendMessage({ dispatch, commit }, { channelID, senderID, senderType = 7, messageBody }) {
        const controller = "OrgMessages";
        const Values = [
          { Name: "MessageChannelID", NewValue: channelID, Modified: true },
          { Name: "SenderUserID", NewValue: senderID, Modified: true },
          { Name: "SenderTerminalTypeID", NewValue: senderType, Modified: true }, // 7 is parent terminal Type by default
          { Name: "MessageBody", NewValue: messageBody, Modified: true },
        ];
        dispatch("InsertRequest", { controller, Values }, {root: true})
          .then((response) => {
            const messageID = response.d.Values[0].NewValue;
            //dispatch("setAlert", { type: "success", title: "New Message", text: "New Message Sent" }, {root: true});
            return messageID;
          })
          .catch((error) => {
            dispatch("setAlert", { type: "error", title: "Message Channel", text: error }, {root: true});
          });
      },
    
};

const getters = {};

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
};
