            hover
            align="center"
            color="blue-lighten-2"
            v-bind="activatorProps"
          >
            <v-row align="center" justify="center" class="h-100">
              <v-btn icon="mdi-plus" size="x-large" class="my-auto"></v-btn>
            </v-row> </v-card
        ></v-col>
      </template>
      <!-- <v-stepper
        alt-labels
        v-model="currentStep"
        :items="['Child Info']"
      >
        <template v-slot:item.1> -->
          <v-card flat class="pa-5">
            <v-card-title>Add New Child</v-card-title>
            <v-text-field
              v-model="child.first_name"
              label="Child first name"
              hide-details
              required
              class="my-2"
            ></v-text-field>
            <v-text-field
              v-model="child.last_name"
              label="Child last name"
              hide-details
              required
              class="my-2"
            ></v-text-field>
            <v-select
              v-model="child.gender"
              :items="genders"
              item-title="name"
              item-value="id"
              label="Gender"
              required
            ></v-select>
            <v-file-input
            v-model="child.image"
            prepend-icon=""
            label="Profile Picture"
            accept="image/*"
            color="deep-purple-accent-4"
            counter
            ></v-file-input>
            <v-text-field type="date" v-model="child.birth_date" required></v-text-field>
            
            <!-- <date-picker title="Date of birth" @date="getDate" /> -->
            <v-btn color="success" @click="addChild()" class="ma-6 mx-auto w-100" prepend-icon="mdi-plus">
                Add
              </v-btn>
          </v-card>
        <!-- </template> -->
        <!-- <template v-slot:item.2>
          <v-card flat>
            <v-select
              v-model="value"
              :items="items"
              label="Medical Notes"
              chips
              multiple
            ></v-select>
            <v-select
              v-model="hobbiesValue"
              :items="hobbies"
              label="Hobbies"
