            ></v-select>
            <v-row>
              <v-spacer></v-spacer>
              <v-btn variant="tonal" @click="addChild()" class="ma-3">
                Add
              </v-btn>
            </v-row>
          </v-card>
        </template> -->
      <!-- </v-stepper> -->
    </v-dialog>
  </div>
</template>
<script>
import DatePicker from "../global/DatePicker.vue";
import { useAxios } from '@/composables/useAxios';

export default {
  components: { DatePicker },
  created()
  {
    this.fetchGenders();
  },
  methods: {
    ...useAxios(),
    async fetchGenders() {
      this.genders = await this.get('genders')
    },
    parseChild(values) {
      const child = {};
      values.forEach((item) => {
        child[item.Name] = item.NewValue;
      });
      return child;
    },
    getDate(date) {
      this.child.birth_date = date;
      // alert(this.bDate);
    },
    genderDisplay() {
      // Map numeric values to string display for the user
      return this.gender.map((value, index) => ({
        text: value,
        value: index + 1, // Assuming API values start from 1
      }));
    },

    async addChild() {
      const formData = new FormData();
      Object.keys(this.child).forEach(key => {
        if (key == 'image')
        {
          if (this.child[key][0])
            formData.append(key, this.child[key][0]);
        }
        else
          formData.append(key, this.child[key]);
      });
      if (this.child.birth_date == null)
      {
        this.$store.dispatch("setAlert", {type: "error",  title: "Adding a new child",  text: "Please write a valid date of birth",});
        return;
      }
      const response = await this.post('children', formData, {'Content-Type': 'multipart/form-data',})
      if (response.id)
      {
        this.$store.dispatch("setAlert", {type: "success",  title: "Adding a new child",  text: "New child added successfully",});
      }  
      else
        this.$store.dispatch("setAlert", {type: "error",  title: "Adding a new child",  text: "Error Adding a new child.",});

      this.$store.state.children.children = await this.get('children');
      this.dialog = false;
    },

  },
  data: () => ({
    currentStep: 0,
    dialog: false,
    firstName: "",
    lastName: "",
    files:[],
    bDate: null,
    gender: [["Male", "Female"]],
    genderValue: "null",
    child: {
        first_name: '',
        last_name: '',
        birth_date: null,
        gender: 1,
        image: [],
        // Include other fields as necessary
      },
      genders: [], // Fetch these from your API
      classrooms: [], // Fetch these from your API

    items: [
      "Asthma",
      "Food Allergies",
      "Attention Deficit Hyperactivity Disorder (ADHD)",
      "Autism Spectrum Disorder (ASD)",
      "Celiac Disease",
      "Down Syndrome",
