<template>
  <v-container class="pa-5 mt-3 h-100">
    <v-breadcrumbs :items="breads">
      <template v-slot:prepend>
        <v-icon size="small" icon="$vuetify" class="text-primary"></v-icon>
      </template>
    </v-breadcrumbs>
    <v-row no-gutters v-if="childFound">
      <v-col cols="12">
        <v-card rounded="lg" flat class="h-100">
          <v-tabs v-model="tab" color="deep-purple-accent-4" align-tabs="start" show-arrows>
            <v-tab
              v-for="(name, idx) in tabs"
              :value="idx"
              :key="idx"
              class="text-none"
              >{{ name }}</v-tab
            >
          </v-tabs>
          <v-window v-model="tab">
            <v-window-item value="0">
              <ChildProfile />
            </v-window-item>
            <v-window-item value="1">
              <Guardians />
            </v-window-item>
            <v-window-item value="2">
              <ContactsTab />
            </v-window-item>
            <v-window-item value="3">
              <MedicalRecords />
            </v-window-item>
            <!-- <v-window-item value="4">
              <notes-tab />
            </v-window-item> -->
            <v-window-item value="4">
              <documents-tab />
            </v-window-item>
            <v-window-item value="5">
              <SubsidyTab />
            </v-window-item>
            <v-window-item value="6">
              <AdmissionRequestTab />
            </v-window-item>
          </v-window>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ChildProfile from "../components/child/ChildProfile.vue";
import ContactsTab from "../components/child/ContactsTab/ContactsTab.vue";
import DocumentsTab from "../components/child/DocumentsTab.vue";
import Guardians from "../components/child/GuardiansTab/GuardiansTab.vue";
import MedicalRecords from "../components/child/MedicalTab/MedicalRecords.vue";
import NotesTab from "../components/child/NotesTab.vue";
import SubsidyTab from "../components/child/SubsidyTab/SubsidyTab.vue";
import AdmissionRequestTab from "../components/child/AdmissionRequestTab.vue";
import { useAxios } from '@/composables/useAxios';
export default {
  components: {
    ChildProfile,
    Guardians,
    ContactsTab,
    MedicalRecords,
    NotesTab,
    DocumentsTab,
    SubsidyTab,
    AdmissionRequestTab,
  },
  async created() {
    this.childId = this.$route.params.id;
    await this.fetchChildData();
    const existingChild = this.$store.state.children.children.find(
      (c) => c.id === this.childId
    );
    if (existingChild) {
      this.childId = existingChild.id;
      this.childFound = true;
    } else {
      // Redirect to another route if child data is not in store
      this.$store.state.children.children = await this.get('children');
      const existingChild = this.$store.state.children.children.find((c) => c.id === this.childId);
      if (existingChild)
      {
        this.childId = existingChild.id;
        this.childFound = true;
      }else
      {
        this.$router.replace("/children");
      }     
    }
  },
  methods:
  {
    ...useAxios(),
    async fetchChildData()
    {
      const children = this.$store.state.children.children;
      if (children.length < 1)
      {
        this.$store.state.children.children = await this.get('children');
      }else
      {
        this.child = await this.get('children/' + this.childId)
      }
    }
  },
  data() {
    return {
      tab: null,
      tabs: [
        "Profile",
        "Guardians",
        "Contacts",
        "Medical Records",
        // "Notes",
        "Documents",
        "Subsidies",
        "Admission Requests",
        // "Treatments",
      ],
      childId: null,
      child: null,
      childFound: false,
      breads: [
        {
          title: "Dashboard",
          disabled: false,
          href: "/",
        },
        {
          title: "Children",
          disabled: false,
          href: "/children",
        },
        {
          title: "Child",
          disabled: true,
          href: "",
        },
      ],
    };
  },
};
</script>

<style scoped></style>
