<template>
  <div class="pa-4 text-center">
    <v-dialog v-model="localDialogOpen" max-width="600">
      <template v-slot:activator="{ on, attrs }">
        <v-btn class="text-none font-weight-regular" prepend-icon="mdi-plus" text="Add Medical Record" variant="text"
          color="green" v-bind="attrs" v-on="on" @click="openDialog">
        </v-btn>
      </template>

      <v-card prepend-icon="mdi-plus" title="Add Medical Report">
        <v-card-text>
          <v-row dense>
            <v-col cols="12" class="mt-2">
              <v-text-field v-model="situation" label="Medical Situation*" required></v-text-field>
            </v-col>

            <v-col cols="12">
              <v-text-field v-model="notes" label="notes" @keydown.enter="saveMedicalRecord"></v-text-field>
            </v-col>

            <v-col cols="12">
              <v-switch v-model="needTreatment" label="Need Treatment" hide-details inset color="primary"></v-switch>
            </v-col>

            <v-col cols="12" class="mt-2">
              <v-text-field v-model="dosage" label="Dosage" ></v-text-field>
            </v-col>

            <v-col cols="12" class="mt-2">
              <v-text-field v-model="times" label="Times" type="number"></v-text-field>
            </v-col>

            

          </v-row>

          <small class="text-caption text-medium-emphasis">*indicates required field</small>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn text="Close" variant="plain" @click="closeDialog">Close</v-btn>

          <v-btn color="primary" text="Save" variant="tonal" @click="saveMedicalRecord">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  props: {
    value: Boolean,
    medicalRecord: {
      type: Object,
      default: () => ({
        situation: '',
        notes: '',
        needTreatment: false,
        dosage: null,
        times: 1,
      })
    }
  },
  data() {
    return {
      localDialogOpen: this.value,
      situation: this.medicalRecord.situation,
      notes: this.medicalRecord.notes,
      needTreatment: this.medicalRecord.needTreatment,
      dosage: this.medicalRecord.dosage,
      times: this.medicalRecord.times,

    };
  },
  watch: {
    value(val) {
      this.localDialogOpen = val;
    },
    localDialogOpen(val) {
      if (!val) {
        this.$emit('input', false); // Notify the parent component to update the dialogOpen variable
        this.resetFields();
      }
    },
    medicalRecord: {
      immediate: true,
      handler(newValue) {
        this.situation = newValue.situation;
        this.notes = newValue.notes;
        this.needTreatment = newValue.needTreatment;
        this.dosage = newValue.dosage;
        this.times = newValue.times;
      }
    }
  },
  methods: {
    resetFields()
    {
      this.situation = '';
      this.notes = '';
      this.needTreatment = false;
      this.dosage = null;
      this.times = 0;
    },
    openDialog() {
      this.$emit('input', true);
    },
    closeDialog() {
      this.$emit('input', false);
    },
    saveMedicalRecord() {
      this.$emit('save', {
        situation: this.situation,
        notes: this.notes,
        needTreatment: this.needTreatment,
        dosage: this.dosage,
        times: this.times,
      });
      this.closeDialog();
    }
  }
};
</script>
