<template>
  <div class="pa-4 text-center">
    <v-dialog v-model="localDialogOpen" max-width="600">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="text-none font-weight-regular"
          prepend-icon="mdi-plus"
          text="Add Subsidy"
          variant="text"
          color="green"
          v-bind="attrs"
          v-on="on"
          @click="openDialog"
        >
        </v-btn>
      </template>

      <v-card prepend-icon="mdi-plus" title="Add Subsidy">
        <v-card-text>
          <v-row dense>
            <v-col cols="12" class="mt-2">
              <v-text-field
                v-model="formattedstart_date"
                label="Start Date*"
                readonly
                append-icon="mdi-calendar"
                @click:append="showstart_datePicker = !showstart_datePicker"
                @click="showstart_datePicker = !showstart_datePicker"
                required
              ></v-text-field>
              <v-date-picker v-if="showstart_datePicker" v-model="start_date" @input="showstart_datePicker = false"></v-date-picker>
            </v-col>

            <v-col cols="12" class="mt-2">
              <v-text-field
                v-model="formattedend_date"
                label="End Date*"
                readonly
                append-icon="mdi-calendar"
                @click:append="showend_datePicker = !showend_datePicker"
                @click="showend_datePicker = !showend_datePicker"
                required
              ></v-text-field>
              <v-date-picker v-if="showend_datePicker" v-model="end_date" @input="showend_datePicker = false"></v-date-picker>
            </v-col>

            <v-col cols="12">
              <v-text-field v-model="discount_ratio" label="Discount Ratio*" required @keydown.enter="saveSubsidy"></v-text-field>
            </v-col>

            <v-col cols="12">
              <v-file-input v-model="document" label="document Upload"></v-file-input>
              <div>
                <span>Current document: <a v-if="documentName" :href="documentName">{{ documentName.split('/').pop() }}</a></span>
              </div>
            </v-col>
          </v-row>

          <small class="text-caption text-medium-emphasis">*indicates required field</small>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn text="Close" variant="plain" @click="closeDialog">Close</v-btn>

          <v-btn color="primary" text="Save" variant="tonal" @click="saveSubsidy">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  props: {
    value: Boolean,
    subsidy: {
      type: Object,
      default: () => ({
        id: null,
        start_date: '',
        end_date: '',
        discount_ratio: '',
        document: null,
        documentName: null,
      })
    }
  },
  data() {
    return {
      localDialogOpen: this.value,
      id: null,
      start_date: null,
      end_date: null,
      discount_ratio: '',
      document: null,
      documentName: null,
      showstart_datePicker: false,
      showend_datePicker: false
    };
  },
  computed: {
    formattedstart_date() {
      return this.start_date ? this.formatDate(this.start_date) : '';
    },
    formattedend_date() {
      return this.end_date ? this.formatDate(this.end_date) : '';
    }
  },
  watch: {
    value(val) {
      this.localDialogOpen = val;
      if (!val) {
        // Reset fields when closing dialog
        this.resetFields();
      }
      
    },
    localDialogOpen(val) {
      if (!val) {
        this.$emit('input', false); // Notify the parent component to update the dialogOpen variable
      }
    },
    subsidy: {
      immediate: true,
      handler(newValue) {
        this.start_date = newValue.start_date ? new Date(newValue.start_date) : null;
        this.end_date = newValue.end_date ? new Date(newValue.end_date) : null;
        this.discount_ratio = newValue.discount_ratio;
        this.document = null;
        this.documentName = newValue.document;
        this.id = newValue.id;
      }
    }
  },
  methods: {
    openDialog() {
      this.localDialogOpen = true;
      this.resetFields();
    },
    closeDialog() {
      this.localDialogOpen = false;
      this.resetFields();
      this.$emit('input', false);
    },
    saveSubsidy() {
      if (!this.start_date || !this.end_date || !this.discount_ratio) {
        this.$store.dispatch("setAlert", { type: "error", title: "Please, make sure to fill all required fields.", text: error });
        return;
      }

      // Ensure dates are formatted correctly
      const formattedstart_date = this.formatDate(this.start_date);
      const formattedend_date = this.formatDate(this.end_date);
      
      this.$emit('save', {
        start_date: formattedstart_date,
        end_date: formattedend_date,
        discount_ratio: this.discount_ratio,
        document: this.document
      });
      this.closeDialog();
    },
    formatDate(date) {
      if (!date) return '';

      // Adjust for timezone offset
      const tzOffset = date.getTimezoneOffset() * 60000; // Offset in milliseconds
      return new Date(date - tzOffset).toISOString().split('T')[0];
    },
    resetFields() {
      this.start_date = null;
      this.end_date = null;
      this.discount_ratio = '';
      this.document = null;
      this.documentName = null;
      this.showstart_datePicker = false;
      this.showend_datePicker = false;
    }
  }
};
</script>
