<template>
    <div class="pa-5 mt-3">
      <v-breadcrumbs :items="breads">
        <template v-slot:prepend>
          <v-icon size="small" icon="$vuetify" class="text-primary"></v-icon>
        </template>
      </v-breadcrumbs>
      <v-row justify="center" align="center">
        <TransitionGroup name="fade" class="container">
          <v-col
            align="center"
            v-for="child in $store.state.children.children"
            :key="child.id"
            cols="12"
            sm="6"
            md="4"
            lg="3"
            xl="2"
          >
            <v-card
              rounded="xl"
              width="300"
              class="pa-5"
              link
              hover
              @click="
                this.$router.push({ name: 'child_activities', params: { id: child.id } })
              "
            >
              <v-card-title align="center">
                <v-avatar
                  class="elevation-10"
                  image="https://cdn-icons-png.flaticon.com/512/7084/7084446.png"
                  size="100"
                ></v-avatar>
                <p class="text-blue-darken-4 text-h5 mt-3">{{ child.name }}</p>
                <p class="text-green text-h7 mt-1">Age {{ child.age }}</p>
              </v-card-title>
              <v-card-text align="center" class="mt-2">
                
                <v-btn class="text-none text-primary" variant="plain"
                  >View
                  <template v-slot:prepend>
                    <v-icon class="text-primary">mdi-eye</v-icon>
                  </template>
                </v-btn>
                
              </v-card-text>
            </v-card>
          </v-col>
  
        </TransitionGroup>
      </v-row>
    </div>
  </template>
  <script>
  export default {
    async created() {
      const url = "GetPage";
      const controller = "MyChildren";
      await this.$store
        .dispatch("makeApiRequest", {
          url,
          controller,
        })
        .then((response) => {
          let children = response.data.d.Rows;
          this.$store.commit("fetchChildren", children);
        })
        .catch((error) => {
          console.error("API Request Error:", error);
        });
    },
    methods: {
      fetchNewChild(child) {
        this.$store.state.children.push(child);
      },
      async deleteChild(id) {
        const url = "Execute";
        const controller = "MyChildren";
        const Values = [
          {
            Name: "ChildID",
            OldValue: id,
          },
        ];
        this.$store
          .dispatch("DeleteRequest", {
            url,
            controller,
            Values,
          })
          .then((response) => {
            this.$store.state.children = this.$store.state.children.filter(
              (child) => child.id != id
            );
          })
          .catch((error) => {
            console.error("Children Delete Error:", error);
            this.$store.dispatch("setAlert", {
                type: "error",
                title: "New Child Error",
                text: error,
              });
          });
      },
    },
    data() {
      return {
        children: [],
        breads: [
          {
            title: "Dashboard",
            disabled: false,
            href: "/",
          },
          {
            title: "Children",
            disabled: true,
            href: "/children",
          },
        ],
      };
    },
  };
  </script>
  
  <style scoped lang="scss">
  $avatar-elevation: 10;
  
  .profile-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 400px;
    width: 100%;
    border-radius: 25px;
    padding: 30px;
    border: 1px solid #ffffff40;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4);
  }
  .image {
    position: relative;
    height: 100px;
    width: 100px;
  }
  .image .profile-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4);
  }
  .data {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 15px;
  }
  .data h2 {
    font-size: 33px;
    font-weight: 600;
  }
  span {
    font-size: 18px;
  }
  .row {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  .row .info {
    text-align: center;
    padding: 0 20px;
  }
  .buttons {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  .buttons .btn {
    color: #fff;
    text-decoration: none;
    margin: 0 20px;
    padding: 8px 25px;
    border-radius: 25px;
    font-size: 18px;
    white-space: nowrap;
    background: linear-gradient(to left, #33ccff 0%, #ff99cc 100%);
  }
  .buttons .btn:hover {
    box-shadow: inset 0 5px 20px rgba(0, 0, 0, 0.4);
  }
  </style>
  