<template>
  <v-snackbar
    :color="snackbarColor"
    rounded="pill"
    :timeout="2000"
    v-model="this.$store.state.alert"
    v-if="this.$store.state.alert"
    transition="fade-transition"
  >
    {{ this.$store.state.alertText }}
  </v-snackbar>
</template>

<script>
export default {
  computed: {
    snackbarColor() {
      switch (this.$store.state.alertType) {
        case 'success':
          return 'light-blue';
        case 'info':
          return 'blue-grey';
        case 'error':
          return 'red darken-1';
        default:
          return 'blue-grey';
      }
    },
  },
};
</script>
