<template>
  <div class="px-5">
    <v-card
      rounded="xl"
      class="text-start"
      v-for="comment in comments"
      :key="comment.commenter_image"
      flat
    >
      <template v-slot:prepend>
        <v-avatar size="40" color="red" :image="comment.commenter_image">
        </v-avatar>
      </template>
      <template v-slot:title>
        <v-row>
          <v-col>
            <p class="text-blue">{{ comment.commenter }} <v-chip :color="comment.role_color" label size="x-small"> {{comment.role}} </v-chip></p> 
          </v-col>
          <v-spacer></v-spacer>
          <v-col align="end">
            <!-- TODO: options -->
          </v-col>
        </v-row>
      </template>
      <template v-slot:subtitle>
        <p class="post-content">
          {{ comment.content }}
        </p>
        <p class="post-actions">
          <span class="me-2 text-blue">Like</span>
          <span class="me-2 text-blue">Reply</span>
          <span class="me-2 text-blue">Translate</span>
          <span class="me-2" style="font-size: 0.8em">{{ comment.from }}</span>
        </p>
      </template>
    </v-card>
  </div>
</template>

<script>
export default {
  props: ["comments"],
  data() {
    return {};
  },
};
</script>
