            <span class="me-4"><v-icon right size="small">mdi-account-supervisor</v-icon> {{center.teachers_count}} Teachers</span>
          </span>
        </template>
        <v-card-text>
          <!-- Carousel and Data -->
          <v-row>
            <v-col cols="12" md="6" lg="4">
              <v-carousel cycle hide-delimiter-background height="400">
                <v-carousel-item rounded="lg" v-for="(item, i) in images" :key="i" :src="item.src"
                  cover></v-carousel-item>
              </v-carousel>
            </v-col>
            <v-col cols="12" md="6" lg="8">
              <h2>About {{ center.name }}</h2>
              <p class="">
                {{center.desc}}
              </p>
              <br />
              <!-- Cetner Data -->
              <v-row>
                <!-- <v-col>
                  <v-icon right size="small">mdi-currency-usd</v-icon> Average
                  Cost <br />
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    $750
                  </p>
                </v-col> -->
                <v-col>
                  <v-icon right size="small">mdi-chart-line</v-icon> Age Range
                  <br />
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    {{ center.start_age }} : {{ center.end_age }} Years
                  </p>
                </v-col>
                <v-col>
                  <v-icon right size="small">mdi-school</v-icon> Class Rooms
                  <br />
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    {{center.classroom_count}}
                  </p>
                </v-col>
                <v-col>
                  <v-icon right size="small">mdi-account-supervisor</v-icon> Teachers
                  <br />
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    {{center.teachers_count}}
                  </p>
                </v-col>
              </v-row>
              <v-row>

              </v-row>
              <!-- <v-row>
                <v-col>
                  <v-icon right size="small">mdi-human-child</v-icon> Children
                  <br />
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    120
                  </p>
                </v-col>
                <v-col>
                  <v-icon right size="small">mdi-account-supervisor</v-icon>
                  Teachers <br />
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    20
                  </p>
                </v-col>
                <v-col>
                  <v-icon right size="small">mdi-bus</v-icon> Buses <br />
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    15
                  </p>
                </v-col>
              </v-row> -->
              <!-- <v-divider :thickness="2" class="my-2"></v-divider> -->
              <!-- <v-row>
                <v-col>
                  <img src="   https://cdn-icons-png.flaticon.com/512/7959/7959219.png " width="30" height="30" alt=""
                    title="" class="img-small" />
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    Potential value
                  </p>
                  <v-chip color="success" label> High Confidence </v-chip>
                </v-col>
                <v-col>
                  <p>Low Range</p>
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    $550
                  </p>
                </v-col>
                <v-col>
                  <p>Med Range</p>
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    $750
                  </p>
                </v-col>
                <v-col>
                  <p>High Range</p>
                  <p class="font-weight-bold text-black text-under-icon ms-1">
                    $1250
                  </p>
                </v-col>
              </v-row> -->
            </v-col>
          </v-row>

          <!-- Map and inquiring -->
          <v-row>
            <v-col>
              <v-row>
                <!-- Map -->
                <v-col cols="12" md="4" align="center">
                  <div id="map" class="w-100 rounded-lg" style="height: 350px;"></div>
                  <v-btn color="primary" class="text-none me-2" icon="mdi-car" size="x-small"
                    @click.stop="viewDirection('driving')" />
                  <v-btn color="primary" class="text-none" icon="mdi-walk" size="x-small"
                    @click.stop="viewDirection('walking')" />

                </v-col>
                <!-- Apply to the center -->
                <v-col cols="12" md="8">
                  <v-card rounded="lg" class="pa-4" outlined elevated>
                    <b class="text-black">Want to apply for one of your children to this center?
                    </b>
                    <p class="mt-3">select one of your children</p>
                    <v-combobox v-model="select" :items="items" label="Select children" multiple>
                      <template v-slot:selection="data">
                        <v-chip :key="JSON.stringify(data.item)" v-bind="data.attrs" :disabled="data.disabled"
                          :model-value="data.selected" size="small" @click:close="data.parent.selectItem(data.item)">
                          <template v-slot:prepend>
                            <v-avatar class="bg-primary text-uppercase" start>{{ data.item.title.slice(0, 1)
                              }}</v-avatar>
                          </template>
                          {{ data.item.title }}
                        </v-chip>
                      </template>
                    </v-combobox>
                    <v-dialog v-model="questionDialog" 
                    :class="{
                      'w-75': !$vuetify.display.mdAndDown,
                      'w-100': $vuetify.display.mdAndDown
                      }">
                      <v-card class="pa-5" elevation="10" rounded="xl">
                        <v-card-title>Answer these questions before apply</v-card-title>
                        <v-card-text>
                          <v-form ref="questionForm" v-model="isFormValid">
                            <v-row v-for="(question, index) in center.questions" :key="index">
                              <v-col cols="12">
                                <v-text-field v-if="question.type === 'text'" v-model="answers[question.id]"
                                  :label="question.text" :required="!question.optional" :rules="question.optional ? [] : [v => !!v || 'This field is required']"></v-text-field>
                                <v-select v-else-if="question.type === 'multiple_choice'" v-model="answers[question.id]"
                                  :items="question.choices_list" :label="question.text"
                                  :required="!question.optional" :rules="question.optional ? [] : [v => !!v || 'This field is required']"></v-select>
                              </v-col>
                            </v-row>
                            <v-row>
                              <v-btn append-icon="mdi-send" color="#5865f2" variant="flat" class="text-none mx-auto" @click="applyChildren">Apply</v-btn>
                            </v-row>
                          </v-form>
                          
                          
                        </v-card-text>
                      </v-card>
                    </v-dialog>
                    <v-row align="end">
                      <v-col align="end">
                        <v-btn append-icon="mdi-send" color="#5865f2" variant="flat" class="text-none"
                          @click="openQuestions()">Apply</v-btn>
                      </v-col>
                    </v-row>
                  </v-card>
                  <v-card rounded="lg" class="pa-4 mt-4" outlined eleveated>
                    <p class="text-black text-center font-weight-bold">Have any questions?</p>
                    <p class="text-black text-center font-weight-bold">Contact with us</p>
                    <center>
                      <v-btn class="btn-grad w-50" icon="mdi-message-text" @click="createConversation()"></v-btn>
                    </center>
                  </v-card>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-card-text>

        <template v-slot:actions>
          <v-btn class="ms-auto" text="Ok" @click="$store.state.viewCenterDialog = false"></v-btn>
        </template>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { useAxios } from '@/composables/useAxios';
export default {
  mounted() {
    this.initMap();
  },
  async created() {
    this.center = this.$store.state.selectedCenter;
    
    this.items.length = 0;
    this.$store.state.children.children.forEach(async child => {
      const admission_requests = await this.get('children/' + child.id + '/admission-requests')
      const has_this_center_request = admission_requests.some(request => request.business_unit == this.center.id);
      if (!has_this_center_request && child.age >= this.center.start_age && child.age <= this.center.end_age) {
        this.items.push(child.first_name);
      }
    });
  },
  data() {
    return {
      dialog: false,
      center: null,
      questionDialog:false,
      answers: {},
      isFormValid: false,
      images: [
        {
          src: "https://cdn.britannica.com/24/141224-050-0F5FA19C/Caregivers-children-day-care-centre.jpg",
        },
        {
          src: "https://procaresoftware.com/wp-content/uploads/2020/11/AdobeStock_65093057_Resized-1024x682.jpeg",
        },
        {
          src: "https://www.business-services.upenn.edu/sites/default/files/styles/800x800/public/2018-12/PCC%202_0.JPG?itok=Af4gNU3A",
        },
        {
          src: "https://sftool.gov/Content/attachments/PWUSCG_Classroom.jpg",
        },
      ],
      select: [],
      items: [],
      selectedChildren: [],
    };
  },
  methods:
  {
    ...useAxios(),
    openQuestions()
    {
      if (this.select.length > 0 )
      {
        this.questionDialog = true;
      }else
      {
          this.$store.dispatch("setAlert", {
            type: "error",
            title: "Child Applying",
            text: "You must select a child to apply"
          });
          return;
      }
    },
    viewDirection(mode) {
            const travelMode = mode === 'driving' ? 'driving' : 'walking';
            const url = `https://www.google.com/maps/dir/?api=1&origin=${this.$store.state.userLocation.lat},${this.$store.state.userLocation.lng}&destination=${this.center.latitude},${this.center.longitude}&travelmode=${travelMode}`;
            window.open(url, '_blank');
      },
    async createConversation()
    {
      const data = { contact: {id : this.center.id, type: 'businessunit'} };
      try{
        const response = await this.post('conversations', data);
        this.$router.push({ name: 'messages', query: { c: response.data.id }  });
      }catch(error){
        this.$router.push({ name: 'messages', query: { c: error.response.data.id }  });
      };
      
  },
    async applyChildren() {

      this.$refs.questionForm.validate();
      if (this.isFormValid) {
        if (!(this.select.length > 0)) {
          this.$store.dispatch("setAlert", {
            type: "error",
            title: "Child Applying",
            text: "You must select a child to apply"
          });
          return;
        }
        this.$store.dispatch("setAlert", {
          type: "success",
          title: "Children Applied",
          text: "Your children has been applied to " + this.center.name
        });
        this.$store.state.viewCenterDialog = false;
        const childrenCount = this.select.length

        if (childrenCount > 0)
        {
          this.selectedChildren = this.$store.state.children.children.filter(child => 
            this.select.includes(child.first_name)
          );
        }
        this.selectedChildren.forEach( async child =>
          {
            const data = {business_unit : this.center.id, type: 'under_review', answers: this.answers}
            await this.post('children/' + child.id + '/admission-requests', data)
          }
        )

        // const BusinessUnitTerminalType = 1;
        // let message = '';
        // if (childreCount == 1)
        //   message = `Welcome to ${this.center.name}, we are happy to inform you that your request about your child <b> ${this.select[0]} </b> has been received to our center, and we will review it as soon as possible.`
        // else {
        //   let childrenText = '';

        //   if (this.select.length > 1) {
        //     childrenText = this.select.slice(0, -1).join(', ') + ' and ' + this.select.slice(-1) + " ";
        //   } else if (this.select.length === 1) {
        //     childrenText = this.select[0];
        //   }
        //   message = `Welcome to ${this.center.name}, we are happy to inform you that your request about your children <b> ${childrenText} </b> has been received to our center, and we will review it as soon as possible.`
        // }
        // const channelID = await this.$store.dispatch('messages/setMessageChannel', { firstId: this.center.id, firstType: BusinessUnitTerminalType, allowReply: true });
        // const messageID = await this.$store.dispatch('messages/sendMessage', { channelID: channelID, senderID: this.center.id, senderType: BusinessUnitTerminalType, messageBody: message })
        this.$store.dispatch('sendNotification', { title: this.center.name, body: `Welcome to ${this.center.name}, we are happy to inform you that...` });
      } else {
        this.$store.dispatch("setAlert", {
          type: "error",
          title: "Child Applying",
          text: "Please reply all questions"
        });
      }


      
    },
    initMap() {
      // Initialize the map
      const map = new google.maps.Map(document.getElementById('map'), {
        zoom: 14,
        center: { lat: 0, lng: 0 }, // Temporary center, will be updated by geocoder
      });

      // Create a geocoder instance
      const geocoder = new google.maps.Geocoder();

      // Geocode the address
      geocoder.geocode({ address: this.center.address }, (results, status) => {
        if (status === 'OK') {
          // Center the map on the geocoded address
          map.setCenter(results[0].geometry.location);
          const location = results[0].geometry.location;
          this.center.latitude = location.lat(); 
          this.center.longitude = location.lng(); 
          // Create a red marker at the address location
          new google.maps.Marker({
            map: map,
            position: results[0].geometry.location,
            title: this.center.address,
          });
        } else {
          console.error('Geocode was not successful for the following reason: ' + status);
        }
      });
    },
  }
};
</script>

<style scoped>
.icons {
  font-size: 0.7em;
}

.text-under-icon {
  font-size: 18px;
}

.btn-grad {
  background-image: linear-gradient(to right, #004FF9 0%, #d1cc40 51%, #004FF9 100%);
  margin: 10px;
  padding: 15px 45px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  display: block;
}

.btn-grad:hover {
  background-position: right center;
  /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
</style>
