const state = {
    children: []
};

const mutations = {
    fetchChildren(state, children) {
        state.children = [];
        children.forEach((child) => {
            state.children.push({
                id: child[14],
                name: child[0],
                image: "https://www.shutterstock.com/image-photo/portrait-young-mixed-race-child-600nw-509909488.jpg",
                gender: child[4],
                attendance: 35,
                social: 13,
                exams: 2,
                age: Math.floor((new Date() - new Date(child[3])) / (1000 * 60 * 60 * 24 * 365.25)),
            });
        });
    },
    removeChild(state, id) {
        state.children = state.children.filter(child => child.id !== id);
    }

};

const actions = {
    async getChildren({ dispatch, commit }) {
        try {
            const url = "GetPage";
            const controller = "MyChildren";
            const response = await dispatch("makeApiRequest", {  // Specify the global action type
                url,
                controller,
            }, { root: true });  // Add { root: true } to dispatch the action from the root store
            const children = response.data.d.Rows;
            commit("fetchChildren", children);
            // Assuming 'loading' is a state in your component, you may want to manage it here
            // this.loading = false;
        } catch (error) {
            console.error("API Request Error:", error);
        }
    },
    async deleteChild({ dispatch, commit, state }, id) {
        try {
            const url = "Execute";
            const controller = "MyChildren";
            const Values = [
                {
                    Name: "ChildID",
                    OldValue: id,
                },
            ];
            await dispatch("DeleteRequest", {
                url,
                controller,
                Values,
            }, { root: true }); // Dispatching the DeleteRequest action from the root store
            commit("removeChild", id); // Committing mutation to remove the child from state after successful deletion
        } catch (error) {
            console.error("Children Delete Error:", error);
            dispatch("setAlert", {
                type: "error",
                title: "New Child Error",
                text: error,
            }, { root: true }); // Dispatching setAlert action from the root store for error handling
        }
    }

};

const getters = {};

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters,
};
