<template>
  <v-card class="mx-auto">
    <v-row style="height: 70px" align="center">
      <v-spacer></v-spacer>
      <document-dialog  @addDocument="addDocument"/>
 

    </v-row>

    <TransitionGroup name="fade" class="container">
          <v-list lines="three">
            <v-list-item 
              v-for="document in documents" 
              :key="document.id"
              :title="document.file_name"
              :subtitle="'Size: ' + document.file_size + 'KB'"
              :value="document.id"
            >
              <template v-slot:prepend>
                <v-icon :color="getFileColor(document.extension)" class="file-icon">
                  {{ getFileIcon(document.extension) }}
                </v-icon>
              </template>
              <template v-slot:append="{ item }">
                <v-btn color="blue" icon="mdi-eye" variant="text" @click="viewFile(document)"></v-btn>
                <v-btn color="green" icon="mdi-download" variant="text" @click.stop="" :href="document.file" target="_blank" download></v-btn>
                <v-btn
                  color="red"
                  icon="mdi-delete"
                  variant="text"
                  @click="deleteRecord(document)"
                ></v-btn>
              </template>
            </v-list-item>      
      </v-list>
    </TransitionGroup>
  </v-card>
</template>

<script>
import DocumentDialog from "./DocumentDialog.vue";
import { useAxios } from "@/composables/useAxios";
export default {
  async created() {
    this.childId = this.$route.params.id;
    await this.fetchDocuments();
  },
  components: {
    DocumentDialog,
  },
  methods: {
    ...useAxios(),
    async fetchDocuments()
    {
      this.documents = await this.get('children/' + this.childId + '/documents')
    },
    async deleteRecord(document) {
      await this.del('children/' + this.childId + '/documents/' + document.id)
      await this.fetchDocuments();
    },
    async addDocument(data)
    { 
      const formData = new FormData();
      Object.keys(data).forEach(key =>
        {
          formData.append(key, data[key]);
        });
      await this.post('children/' + this.childId + '/documents', formData, {'Content-Type': 'multipart/form-data',})
      await this.fetchDocuments();
    },
    editRecord(item) {
      this.$store.commit("editRecord", item);
    },
    getFileIcon(extension) {
      switch (extension) {
        case 'pdf':
          return 'mdi-file-pdf-box';
        case 'doc':
        case 'docx':
          return 'mdi-file-word-box';
        case 'xls':
        case 'xlsx':
          return 'mdi-file-excel-box';
        case 'png':
        case 'jpg':
        case 'jpeg':
        case 'gif':
          return 'mdi-file-image';
        case 'txt':
          return 'mdi-file-document-box';
        default:
          return 'mdi-file';
      }
    },
    getFileColor(extension) {
      switch (extension) {
        case 'pdf':
          return 'red';
        case 'doc':
        case 'docx':
          return 'blue';
        case 'xls':
        case 'xlsx':
          return 'green';
        case 'png':
        case 'jpg':
        case 'jpeg':
        case 'gif':
          return 'orange';
        case 'txt':
          return 'grey';
        default:
          return 'black';
      }
    },
    viewFile(document) {
      let url = document.file;
      switch (document.extension) {
        case 'pdf':
          // Open PDF in a new tab
          window.open(url, '_blank');
          break;
        case 'doc':
        case 'docx':
        case 'xls':
        case 'xlsx':
        case 'ppt':
        case 'pptx':
          // Use Google Docs Viewer for office documents
          window.open(`https://docs.google.com/viewer?url=${encodeURIComponent(url)}&embedded=true`, '_blank');
          break;
        case 'png':
        case 'jpg':
        case 'jpeg':
        case 'gif':
          // Open images in a new tab
          window.open(url, '_blank');
          break;
        case 'txt':
          // Open text files in a new tab
          window.open(url, '_blank');
          break;
        default:
          // Default behavior for unsupported types
          window.open(url, '_blank');
      }
    },
    todo()
    {
      this.$store.dispatch("setAlert", {
              type: "info",
              title: "TODO",
              text: "TODO: Waiting Sync with database.",
            });
    }

  },

  data() {
    return {
      childId : null,
      documents: [],
      items: [
        { type: "subheader", title: "Child Documents" },
        {
          prependIcon: "mdi-file",
          title: "Morning_Activities.pdf",
          value: 0,
          subtitle: "237 KB",
          date: "1/3/2024",
        },
        { type: "divider", inset: true },
        {
          prependIcon: "mdi-certificate",
          title: "Outdoor_Playtime_Certificate.pdf",
          value: 1,
          subtitle: "536 KB",
          date: "5/10/2023",
        },
        { type: "divider", inset: true },
        {
          prependIcon: "mdi-certificate",
          title: "Arts_and_Crafts_Certificate.pdf",
          value: 2,
          subtitle: "764 KB",
          date: "12/7/2022",
        },
        { type: "divider", inset: true },
        {
          prependIcon: "mdi-file",
          title: "Snack_Time.pdf",
          value: 3,
          subtitle: "141 KB",
          date: "8/22/2021",
        },
        { type: "divider", inset: true },
        {
          prependIcon: "mdi-file",
          title: "Nap_Time.pdf",
          value: 4,
          subtitle: "172 KB",
          date: "3/15/2020",
        },
      ],
    };
  },
};
</script>

<style scoped>
.file-icon
{
  opacity: 1;
}
</style>