<template>
  <v-card rounded="xl" title="Create Post" class="text-start">
    <template v-slot:subtitle>
      <v-divider class="mt-5"></v-divider>
    </template>

    <v-card-text>
      <v-textarea
        density="compact"
        variant="solo"
        label="What do you think?"
        single-line
        hide-details
        flat
        clearable
        v-model="postText"
      >
        <template v-slot:prepend>
          <v-avatar
            image="https://templates.iqonic.design/socialv/bs5/vue/dist/img/user-1.99e84db1.jpg"
            size="60"
          ></v-avatar>
        </template>
      </v-textarea>
      <v-divider class="mt-5"></v-divider>
    </v-card-text>
    <v-card-actions>
      <v-chip-group active-class="deep-purple accent-4 white--text" column>
        <v-chip size="large" rounded="lg" @click="triggerFileInput">
          <v-icon start>
            <v-img height="20" src="https://cdn-icons-png.flaticon.com/512/2201/2201587.png"></v-img>
          </v-icon>
          Photo/Video
        </v-chip>
        <v-chip size="large" rounded="lg">
          <v-icon start>
            <v-img height="20" src="https://cdn-icons-png.flaticon.com/512/9131/9131530.png"></v-img>
          </v-icon>
          Tag Teacher
        </v-chip>
        <v-chip size="large" rounded="lg">
          <v-icon start>
            <v-img height="20" src="https://cdn-icons-png.flaticon.com/512/3437/3437379.png"></v-img>
          </v-icon>
          Feeling/Activity
        </v-chip>
      </v-chip-group>
    </v-card-actions>
    <!-- Post Buttons -->
    <v-row align="center" class="pa-3">
      <v-col align="center">
        <v-btn class="w-100" color="primary" v-if="postHasLetters" @click="post">Post</v-btn>
      </v-col>
    </v-row>
    <v-file-input label="File input" variant="outlined"></v-file-input>

    <v-file-input
      v-model="files"
      multiple
      id="fileInput"
      @change="previewFiles"
      style="display: none;"
      accept="image/png, image/jpeg, image/bmp"
    ></v-file-input>
    <!-- Images Preview -->
    <v-row class="pa-3">
      <v-col
        v-for="(file, index) in previewImages"
        :key="index"
        cols="6"
        md="4"
        class="position-relative"
      >
        <v-img
          :src="file"
          :alt="files[index]?.name"
          aspect-ratio="1"
          class="mb-2"
          @click="showImageDialog(index)"
        ></v-img>
        <v-btn
          icon
          small
          class="remove-btn"
          @click="removeImage(index)"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    <!-- Image Dialog -->
    <v-dialog v-model="dialog" max-width="800px" scrollable>
      <v-img
        :src="selectedImage"
        :alt="selectedImage"
        class="mb-2 w-50 h-50 mx-auto"
        cover
      ></v-img>
    </v-dialog>
  </v-card>
</template>

<script>
export default {
  emits: ['addNewPost'],
  data() {
    return {
      postText: "",
      files: [],
      previewImages: [],
      dialog: false,
      selectedImage: null,
    };
  },
  computed: {
    postHasLetters() {
      return this.postText;
    },
  },
  methods: {
    previewFiles() {
      this.previewImages = [];
      for (const file of this.files) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.previewImages.push(e.target.result);
        };
        reader.readAsDataURL(file);
      }
    },
    triggerFileInput() {
      document.getElementById('fileInput').click();
    },
    removeImage(index) {
      this.previewImages.splice(index, 1);
      this.files.splice(index, 1);
    },
    showImageDialog(index) {
      this.selectedImage = this.previewImages[index];
      this.dialog = true;
    },
    post()
    {
      this.$emit('addNewPost', this.postText, this.files);
      this.postText = null;
      this.files = [];
      this.previewImages = [];
    }
  },
};
</script>

<style scoped>
.v-img {
  border-radius: 8px;
  border: 1px solid #ccc;
  transition: 500ms all ease;
}
.v-img:hover {
  cursor:pointer;
  transform: scale(1.1);
}

.position-relative {
  position: relative;
}

.remove-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: rgba(255, 255, 255, 0.7);
}
</style>
