<template>
    <v-dialog v-model="localVisible" :class="dialogClass">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Form</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form ref="form">
              <v-row v-for="(field, key) in fields" :key="key" align="center" justify="space-between">
                <v-col cols="12">  
                  <!-- Display v-file-input for file upload -->
                  <v-row  v-if="field.type === 'file'">
                    <v-col cols="10">
                        <v-file-input
                            v-model="formData[key]"
                            :label="field.label"
                            accept="image/*"
                        ></v-file-input>
                    </v-col>
                    <v-col cols="2">
                        <!-- Display current image link as clickable -->
                        <div v-if="field.type === 'file' && files[key] && typeof files[key].link === 'string' && !files[key].clear">
                            <v-btn class="text-none text-primary" :href="files[key].link" target="_blank" variant="plain"
                                >View
                                <template v-slot:prepend>
                                <v-icon class="text-primary">mdi-eye</v-icon>
                                </template>
                            </v-btn>
                            <br>
                            <v-btn class="text-none text-primary" variant="plain" @click="removeFile(key)"
                                >Clear
                                <template v-slot:prepend>
                                <v-icon class="text-success">mdi-delete</v-icon>
                                </template>
                            </v-btn>
                        </div>
                    </v-col>
                  </v-row>
                  <!-- Display other input types based on field.type -->
                  <v-text-field
                    v-else-if="field.type === 'string'"
                    v-model="formData[key]"
                    :label="field.label"
                    :type="field.inputType"
                  ></v-text-field>
  
                  <v-textarea
                    v-else-if="field.type === 'textarea'"
                    v-model="formData[key]"
                    :label="field.label"
                  ></v-textarea>
  
                  <v-select
                    v-else-if="field.type === 'choice'"
                    v-model="formData[key]"
                    :items="field.choices"
                    :label="field.label"
                  ></v-select>
  
                  <v-checkbox
                    v-else-if="field.type === 'boolean'"
                    v-model="formData[key]"
                    :label="field.label"
                  ></v-checkbox>
  
                  <v-text-field
                    v-else-if="field.type === 'number'"
                    v-model="formData[key]"
                    :label="field.label"
                    type="number"
                  ></v-text-field>
  
                  <v-text-field
                    v-else-if="field.type === 'datetime'"
                    v-model="formData[key]"
                    :label="field.label"
                    type="datetime-local"
                  ></v-text-field>
  
                  <v-text-field
                    v-else-if="field.type === 'date'"
                    v-model="formData[key]"
                    :label="field.label"
                    type="date"
                  ></v-text-field>
  
                  <v-select
                    v-else-if="field.type === 'foreign-key'"
                    v-model="formData[key]"
                    :items="field.choices"
                    :label="field.label"
                    item-value="id"
                    :item-title="field.foreignKeyDisplayField"
                  ></v-select>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="updateVisibility(false)">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveData">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  import { useAxios } from '@/composables/useAxios';
  
  export default {
    props: {
      modelUrl: {
        type: String,
        required: true
      },
      instanceId: {
        type: Number,
        required: true
      },
      foreignKeys: {
        type: Array,
        default: () => []
      },
      visible: {
        type: Boolean,
        required: true
      }
    },
    data() {
      return {
        fields: {},
        formData: {},
        files: {},
        foreignKeyData: {},
        localVisible: this.visible
      };
    },
    computed: {
      dialogClass() {
        return this.$vuetify.display.smAndDown ? 'w-100' : 'w-50';
      },
    },
    watch: {
      visible(val) {
        this.localVisible = val;
        if (val) {
          this.loadInstanceData();
        }
      },
      localVisible(val) {
        this.$emit('updateVisible', val);
      }
    },
    async created() {
      await this.loadModelFields();
      await this.loadForeignKeyData();
      if (this.visible) {
        await this.loadInstanceData();
      }
    },
    methods: {
      updateVisibility(value) {
        this.localVisible = value;
      },
      async loadModelFields() {
        const { options } = useAxios();
        const response = await options(this.modelUrl);
        const modelFields = response.actions.POST;
  
        this.fields = Object.keys(modelFields).reduce((acc, key) => {
          const field = modelFields[key];
          if (!field.read_only) {
            acc[key] = {
              label: field.label || key,
              type: this.mapType(field.type, key),
              inputType: this.mapInputType(field.type),
              choices: field.choices || [],
              foreignKeyDisplayField: ''
            };
          }
          return acc;
        }, {});
      },
      async loadForeignKeyData() {
        const { get } = useAxios();
  
        for (const foreignKey of this.foreignKeys) {
          const response = await get(foreignKey.api);
          this.foreignKeyData[foreignKey.field] = response.map(item => ({
            id: item.id,
            [foreignKey.displayField]: item[foreignKey.displayField]
          }));
  
          if (this.fields[foreignKey.field]) {
            this.fields[foreignKey.field].choices = this.foreignKeyData[foreignKey.field];
            this.fields[foreignKey.field].foreignKeyDisplayField = foreignKey.displayField;
          }
        }
      },
      async loadInstanceData() {
        const { get } = useAxios();
        const response = await get(`${this.modelUrl}/${this.instanceId}`);
  
        // Populate formData with server data
        Object.keys(response).forEach(key => {
          // Handle files (like image) which are returned as URLs
          if (this.fields[key] && this.fields[key].type === 'file' && typeof response[key] === 'string') {
            // Directly set formData to URL to display current image
            this.files[key] = { link: response[key], clear: false };
            this.formData[key] = null;
          } else {
            this.formData[key] = response[key];
            this.files[key] = { link: null, clear: false };
          }
        });
      },
      mapType(type, key) {
        if (this.foreignKeys.some(fk => fk.field === key)) {
          return 'foreign-key';
        }
        switch (type) {
          case 'string':
            return 'string';
          case 'text':
            return 'textarea';
          case 'choice':
            return 'choice';
          case 'boolean':
            return 'boolean';
          case 'file upload':
            return 'file';
          case 'number':
            return 'number';
          case 'datetime':
            return 'datetime';
          case 'date':
            return 'date';
          case 'field':
            return 'string';
          default:
            return 'string';
        }
      },
      mapInputType(type) {
        if (type === 'email') return 'email';
        if (type === 'url') return 'url';
        if (type === 'password') return 'password';
        return 'text';
      },
      async saveData() {
        const { put } = useAxios();
        const formData = new FormData();
  
        // Append formData with updated values
        Object.keys(this.formData).forEach(key => {
          // If it's a file input, append the first file (assuming single file upload)
          if (this.fields[key] && this.fields[key].type === 'file' && typeof this.formData[key] !== 'string') {
            if (this.formData[key])
              formData.append(key, this.formData[key][0]);
            else if (this.files[key].clear)
              formData.append(key, 'clear');
          } else {
            formData.append(key, this.formData[key]);
          }
        });
  
        // Send the formData with proper headers for file upload
        await put(`${this.modelUrl}/${this.instanceId}`, formData, {
          'Content-Type': 'multipart/form-data'
        });
  
        this.updateVisibility(false);
        this.$emit('saved');
      },
      removeFile(key) {
        // Implement logic to remove the file from formData and reset the field
        this.formData[key] = null; // Reset the file field
        this.files[key].clear = true;
      }
    }
  };
  </script>
  
  <style scoped>
  /* Add your styles here */
  </style>
  