import axios from 'axios';
import { Buffer } from 'buffer';

const username = 'parent1';
const password = 'P@$$W0rd';
// const token = '36063b70d279e24f6dfc4576956f4a60940a0c95';
const token = localStorage.getItem("token");
// const token = Buffer.from(`${username}:${password}`, 'utf8').toString('base64');

const apiClient = axios.create({
  baseURL: 'http://courses.psu.edu.eg:8000/api/',
  withCredentials: false,
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json',
    'Authorization': `Token ${token}`,
    // 'Authorization': `Basic ${token}`,
  },
});

export function updateAuthToken(newToken) {
  apiClient.defaults.headers['Authorization'] = `Token ${newToken}`;
}

apiClient.interceptors.response.use(
  response => response,
  error => {
    if (error.response && error.response.status === 401) {
      // Redirect to base URL when 401 Unauthorized is encountered
      window.location.href = "/";
      return Promise.reject(error);
    }
    return Promise.reject(error);
  }
);


export function useAxios() {
  const request = async (method, url, data = null, headers = {}) => {
    const config = {
      method,
      url,
      data,
      headers: { ...apiClient.defaults.headers, ...headers },
    };
    const response = await apiClient(config);
    return response.data;
  };

  const get = async (url, params = {}, headers = {}) => {
    const queryString = new URLSearchParams(params).toString();
    const fullUrl = queryString ? `${url}/?${queryString}` : `${url}/`;
    return await request('get', fullUrl, null, headers);
  };
  
  const post = async (url, data, headers = {}) => {
    return await request('post', url + '/', data, headers);
  };

  const put = async (url, data, headers = {}) => {
    return await request('put', url + '/', data, headers);
  };

  const del = async (url, headers = {}) => {
    return await request('delete', url + '/', null, headers);
  };

  const options = async (url, headers = {}) => {
    return await request('options', url + '/', null, headers);
  };

  return { get, post, put, del, options };
}
